import { coalesceX } from "Dimensions";
import { FoodItemId } from "Objects";
import { Animations } from "./Animations";
import { AssetManager } from "./AssetManager";

type Area2D = {
    x: number;
    y: number;
    width: number;
    height: number;
}

export function renderFoodItemsInArea(area: Area2D, foodItems: FoodItemId[], ctx: CanvasRenderingContext2D){
    let offset = 0;
    for(let foodItem of foodItems) {
        const foodItemAnimation = Animations[`food-items/${foodItem}`];

        const width = Math.min(area.width / Math.max((foodItems.length), 1), coalesceX('2studs'));

        const scale = width / foodItemAnimation.size.width;

        const height = foodItemAnimation.size.height * scale;

        let leftPadding = 0, topPadding = (area.height - height) / 2;

        if(width < area.width && foodItems.length === 1){
            leftPadding = (area.width - width) / 2;
            topPadding = (area.height - height) / 2;
        }

        ctx.drawImage(
            AssetManager.getImage(`food-items/${foodItem}` + `/1.png`),
            area.x + offset + leftPadding,
            area.y + topPadding,
            width,
            height
        )

        offset += (width);
    }
}