import { Controls } from "Controls";
import { coalesceX, coalesceY, Dimension, WIDTH } from "Dimensions";
import { Levels } from "Levels";
import { breadYield, chopYield, FoodItemId, fryYield, Recipe, recipes, spicyBreadYield } from "Objects";
import { create2DRenderer } from "Renderer";
import { GameObject, GameState } from "State";
import { createWithBoundingbox, createWithId, HasBoundingbox, HasId, HasOverlay, HasRenderer } from "Traits";
import { createButton } from "./Button";
import { createGameText } from "./GameText";
import { loadFinishedLevelMenu } from "./loadFinishedLevelMenu";
import { createOverlay } from "./Overlay";


export const loadTutorialScreen = (state: GameState) => {
    let foodItems = state.selectedLevel?.tutorialEntries as FoodItemId[];

    const objects = [
        createOverlay('rgba(255, 255, 255, 0.75)'),
        createGameText({
            text: 'Help',
            color: '#e60e33',
            baseline: 'top',
            align: 'left'
        }, '5%', '5%'),

        ...loadTutorial(foodItems),

        createButton({ text: 'Close', color: '#e60e33', baseline: 'top', align: 'right' }, true, WIDTH - coalesceX('5%'), '5%', handleContinue),
    ]

    function handleContinue(state: GameState): GameState {
        return {
            ...state,
            isPaused: false,
            objects: state.objects.filter(object => !tutorialScreenObjectIds.includes(object.id))
        }
    }

    const tutorialScreenObjectIds = objects.map(object => object.id)

    return objects;
}

export function tutorialScreenReducer(_: GameObject, state: GameState) {
    if (!state.canPause) return state;

    for (let controller of Controls) {
        if (controller.justPressed('tertiaryAction') && !state.isPaused) {
            return {
                ...state,
                isPaused: true,
                objects: [
                    ...state.objects,
                    ...loadTutorialScreen(state)
                ]
            }
        }
    }

    return state
}



function loadTutorial(foodItemIds: FoodItemId[]): GameObject[] {
    let s = createTutorialSprite;
    const tutorial: GameObject[] = [];

    let y: number = coalesceY('12%')

    for (let foodItemId of foodItemIds) {
        let recipe = recipes.find(recipe => recipe.name === foodItemId) as Recipe;
        if (recipe) {
            tutorial.push(
                ...loadRecipe(foodItemId, recipe.ingredientList, y)
            )
            for(let ingredientId of recipe.ingredientList){
                if(canBeCreated(ingredientId)){
                    y += coalesceY('15%')
                    tutorial.push(...loadIngredientCreationProcess(ingredientId, y, 12))
                }
            }
        } else if (canBeCreated(foodItemId)) {
            tutorial.push(
                s('6%', y, `food-items/${foodItemId}`),
                s('12%', y, 'equals'),
                ...loadIngredientCreationProcess(foodItemId, y, 18)
            )
        }

        y += coalesceY('15%')
    }

    return tutorial;
}

function loadRecipe(recipeName: FoodItemId, ingredientList: FoodItemId[], y: Dimension): GameObject[] {
    let s = createTutorialSprite;
    return [
        s('6%', y, `food-items/${recipeName}`),
        s('12%', y, 'equals'),
        ...join(ingredientList.map((foodItemId, index) => (
            s(18 + (6 * (index * 2)) + '%', y, `food-items/${foodItemId}`)
        )), i => s(18 + (6 * (i * 2 - 1)) + '%', y, 'plus'))
    ]
}

function loadIngredientCreationProcess(foodItem: FoodItemId, y: Dimension, indentation: number = 0): GameObject[] {
    let s = createTutorialSprite;

    const steps : string[] = [];

    steps.unshift(`food-items/${foodItem}`)

    while(canBeCreated(foodItem)){

        let record: Partial<Record<FoodItemId, FoodItemId>>, sprite: string;

        if(itemsThatComeFromChopping.includes(foodItem)){
            record = chopYield
            sprite = 'cutting-board'
        } else if(itemsThatComeFromFrying.includes(foodItem)){
            record = fryYield
            sprite = 'fryer/off'
        } else if(itemsThatComeFromBreading.includes(foodItem)){
            record = breadYield
            sprite = 'breading-table'
        } else if(itemsThatComeFromSpicyBreading.includes(foodItem)){
            record = spicyBreadYield
            sprite = 'spicy-breading-table'
        }  else {
            throw Error(`Couldnt figure out how ${foodItem} got created`)
        }

        steps.unshift(sprite);
        foodItem = reverseLookup(record, foodItem);
        steps.unshift(`food-items/${foodItem}`);
    }

    const result = steps.map( (step,i) => s(indentation + (i*2)*6 + '%', y, step))

    return join(result, i => s(indentation + (i*2-1)*6 + '%', y, 'arrow'));
}

function join(array: GameObject[], callback: (i: number) => GameObject): GameObject[] {
    const result: GameObject[] = []
    for (let i = 0; i < array.length; i++) {
        if (i !== 0) result.push(callback(i));
        result.push(array[i]);
    }
    return result
}

const itemsThatComeFromChopping = Object.values(chopYield)
const itemsThatComeFromFrying = Object.values(fryYield)
const itemsThatComeFromBreading = Object.values(breadYield)
const itemsThatComeFromSpicyBreading = Object.values(spicyBreadYield)

function canBeCreated(foodItemId: FoodItemId) {
    return (
        itemsThatComeFromChopping.includes(foodItemId)
        || itemsThatComeFromFrying.includes(foodItemId)
        || itemsThatComeFromBreading.includes(foodItemId)
        || itemsThatComeFromSpicyBreading.includes(foodItemId)
    )
}

function reverseLookup(record: Partial<Record<FoodItemId, FoodItemId>>, foodItemId: FoodItemId): FoodItemId {
    for(let strKey in record){
        const key = strKey as FoodItemId
        if(record[key] === foodItemId){
            return key;
        }
    }
    throw Error("Invalid Reverse Lookup")
}

export type TutorialSprite = HasId & HasRenderer & HasBoundingbox & HasOverlay;

export const createTutorialSprite = (x: Dimension, y: Dimension, sprite: string): TutorialSprite => ({
    ...createWithId(),
    ...createWithBoundingbox('5%', '5%', x, y),
    render: create2DRenderer(sprite),
    hasOverlay: true
})