import { HEIGHT, WIDTH } from "Dimensions";
import { GameState } from "State";
import { hasHitbox, hasOverlay, hasRenderer } from "Traits";

export function createGameRenderer(canvas: HTMLCanvasElement){
    canvas.style.width = '100vw';
    canvas.style.height = '100vh';
    canvas.width = WIDTH;
    canvas.height = HEIGHT;

    const ctx = canvas.getContext("2d") as CanvasRenderingContext2D;

    function renderFrame(state: GameState){
        // Clear the Canvas
        ctx.clearRect(
            0,
            0,
            WIDTH,
            HEIGHT
        );

        // Sort objects with hitboxes on the z-axis
        const objects  = [ ...state.objects ].sort( (a, b) => {
            if(hasOverlay(a) && !hasOverlay(b)){
                return 1
            }
            if(!hasOverlay(a) && hasOverlay(b)){
                return -1
            }
            if(hasHitbox(a) && hasHitbox(b)){
                if(b.hitbox.z == a.hitbox.z) return a.hitbox.y - b.hitbox.y;
                return b.hitbox.z - a.hitbox.z;
            }
            if(hasHitbox(a) && !hasHitbox(b)){
                return 1
            }
            if(!hasHitbox(a) && hasHitbox(b)){
                return -1;
            }
            return 0;
        });

        // Render All Objects
        objects.forEach( object => {
            if(hasRenderer(object)){
                ctx.save();
                object.render(state, object, ctx);
                ctx.restore();
            }
        })
    }

    return renderFrame;
}

