import { Controls } from "Controls";
import { Character, isBreadingTable } from "Objects";
import { AssetManager } from "Renderer";
import { GameObject, GameState, updateGameObjectById } from "State";
import { filter, HasHitBox } from "Traits";
import { canBeControlled } from "./CanBeControlled";
import { canCarry } from "./CanCarry";
import { CanFaceADirection, getObjectsInfrontOf } from "./CanFaceADirection";


export type CanBread = {
    canBread: true;
    breading: boolean;
}

export const canBread = (x: any): x is CanBread => Boolean(x?.canBread)

export const createCanBread = (): CanBread => ({
    canBread: true,
    breading: false,
})

export function canBreadReducer(object: GameObject, state: GameState): GameState {
    if(!canBread(object) || !canBeControlled(object)) return state;

    const controller = Controls[object.controllerId];

    let breader = object as Character;

    let objects = state.objects;

    breader = {
        ...breader,
        breading: false
    }

    if (controller.isPressed('secondaryAction') && breader.carrying === null && !state.isPaused){
        const [ breadingTable ] = getObjectsInfrontOf(breader, filter(state.objects, isBreadingTable));

        if(breadingTable && breadingTable.canBread()){
            breader = {
                ...breader,
                breading: true
            }
            objects = updateGameObjectById(objects, breader)
            objects = breadingTable.bread(breader, objects, state.frameNumber);
            breader = objects.find( object => object.id === breader.id) as Character
        }
    }
    let track = AssetManager.loadAliasedAudio(`breading.${breader.id}`, 'breading.mp3');
    if(breader.breading){
        track.play();
    }

    objects = updateGameObjectById(objects, breader)

    return {
        ...state,
        objects
    }
}
