import { coalesceX, coalesceY, coalesceZ, Pixels, Vector } from "Dimensions";
import { GameObject } from "State";

export type Hitbox = {
    x: Pixels;
    z: Pixels;
    y: Pixels;
    dx: Pixels;
    dz: Pixels;
}

export type HasHitBox = {
    hitbox: Hitbox
}

export const hasHitbox = (x: any): x is HasHitBox => typeof x.hitbox !== 'undefined'

export type Dimension = string | number;

export function createWithHitbox(dx: Dimension, dz: Dimension, x: Dimension = 0, z: Dimension = 0, y: Dimension = 0): HasHitBox {
    return {
        hitbox: {
            dx: coalesceX(dx),
            dz: coalesceZ(dz),
            x: coalesceX(x),
            z: coalesceZ(z),
            y: coalesceY(y)
        }
    }
}

export function moveObject<T extends HasHitBox>(object: T, vector: Vector): T {
    return {
        ...object,
        hitbox: {
            ...object.hitbox,
            x: object.hitbox.x + vector.x as Pixels,
            z: object.hitbox.z + vector.z as Pixels,
        }
    }
}

export function getAjacentObjects(target: HasHitBox, objects: GameObject[]) {
    const padding = coalesceX('1studs')
    const { hitbox } = target;

    const adjustedHitbox: Hitbox = {
        x: hitbox.x - padding as Pixels,
        z: hitbox.z - padding as Pixels,
        y: hitbox.y,
        dx: hitbox.dx + (2 * padding) as Pixels,
        dz: hitbox.dz + (2 * padding) as Pixels,
    }

    return getOverlappingObjects(adjustedHitbox, objects);
}

export function hasCollision(object: HasHitBox, objects: GameObject[]): boolean {
    return getOverlappingObjects(object.hitbox, objects).length > 0
}

export function getOverlappingObjects<T extends GameObject>(hitbox: Hitbox, objects: T[]): T[] {
    return objects.filter((object) => hasHitbox(object) && overlaps(hitbox, object.hitbox))
}

export function overlaps(a: Hitbox, b: Hitbox) {
    return (
        a.x < b.x + b.dx &&
        a.x + a.dx > b.x &&
        a.z < b.z + b.dz &&
        a.dz + a.z > b.z
    )
}