import { GameObject } from "./GameObject";


export function updateGameObjectById(objects: GameObject[], target: GameObject){
    return objects.map( object => object.id === target.id ? target : object );
}

export function updateGameObjectsWhere(objects: GameObject[], filter: (object: GameObject) => boolean, update: <T extends GameObject>(object: T) => GameObject) : GameObject[]{
    return objects.map( object => filter(object) ? update(object) : object)
}

export function removeGameObjectById(objects: GameObject[], target: GameObject){
    return objects.filter( object => object.id !== target.id)
}
