import { coalesceX, Dimension } from "Dimensions";
import { create3DRenderer } from "Renderer";
import { GameObject, removeGameObjectById } from "State";
import { CanBlockMovement, createCanBlockMovement, createWithCounterSpace, createWithHitbox, createWithId, HasRenderer, HasCounterSpace, HasHitBox, HasId, CanHandleDrop, hasHitbox } from "Traits";

export type TrashCan = HasId & HasHitBox & CanBlockMovement & HasRenderer & CanHandleDrop;

export const createTrashCan = (x?: Dimension, z?: Dimension): TrashCan => ({
    ...createWithId(),
    ...createWithHitbox('2studs', '2studs', x, z, '1studs'),
    ...createCanBlockMovement(),
    render: create3DRenderer('trash-can'),
    canHandleDrop,
    handleDropped
})

function canHandleDrop(object: GameObject): object is GameObject & HasHitBox{
    return hasHitbox(object);
}

function handleDropped(object: GameObject, objects: GameObject[]){

    objects = removeGameObjectById(objects, object)

    return objects;
}