import { Dimension } from "Dimensions";
import { loadFinishedLevelMenu } from "Menus";
import { AssetManager, combineRenderers, create3DRenderer, createProgressRenderer, createSpicyIconRenderer } from "Renderer";
import { MS_PER_FRAME } from "startGame";
import { GameObject, GameState, removeGameObjectById, updateGameObjectById } from "State";
import { createWithHitbox, createWithId, HasRenderer, HasHitBox, HasId, CanHandleDrop, hasHitbox, CanBlockMovement, createCanBlockMovement, getObjectsFacing, canBeControlled, filter, canCarry } from "Traits";
import { CanBeSpicy } from "Traits/CanBeSpicy";
import { createFoodItem, FoodItem, FoodItemId, isFoodItem } from "./FoodItem";

type FryableId = 'original-raw-filet' | 'spicy-raw-filet' | 'raw-breaded-nuggets' | 'potato-slices'

const fryableIds = [ 'original-raw-filet', 'spicy-raw-filet', 'raw-breaded-nuggets', 'potato-slices'  ]

const spicyFryableIds = [ 'spicy-raw-filet', 'potato-slices' ]

export type Fryable = FoodItem & {
    foodItemId: FryableId
}

export type Fryer = HasId & HasHitBox & HasRenderer & CanHandleDrop & CanBlockMovement & CanBeSpicy & {
    isFryer: true,
    onFire: boolean,
    content: null | FryableId,
    progress: null | number,
    finishedAt: null | number,
    retrieve: (objects: GameObject[]) => [ GameObject[], FoodItem ]
};


export const createFryer = (x?: Dimension, z?: Dimension): Fryer => ({
    ...createWithId(),
    isSpicy: false,
    isFryer: true,
    content: null,
    progress: null,
    finishedAt: null,
    onFire: false,
    ...createWithHitbox('4studs', '2studs', x, z),
    ...createCanBlockMovement(),
    render: combineRenderers(
        create3DRenderer({ sprite: getSprite, speed: getSpeed }),
        createProgressRenderer('fryer/off'),
        create3DRenderer({ sprite: (fryer: Fryer) => fryer.content != null ? 'steam' : null, speed: 0.5}),
        createSpicyIconRenderer()
    ),
    canHandleDrop,
    handleDropped,
    retrieve
})

const getSprite = (fryer: Fryer, state: GameState) => {
    if(fryer.progress !== null) return `fryer/on`
    if(fryer.finishedAt !== null) {
        const framesElapsed =  state.frameNumber -  fryer.finishedAt;

        const secondsElapased = ((framesElapsed * MS_PER_FRAME) / 1000);

        if(secondsElapased < 12){
            return `fryer/done`
        } else {
            return `fryer/on-fire`
        }
    }
    else return `fryer/off`
}

const getSpeed = (fryer: Fryer, state: GameState) => {
    if(fryer.finishedAt === null) return 1;

    const framesElapsed =  state.frameNumber -  fryer.finishedAt;

    const secondsElapased = ((framesElapsed * MS_PER_FRAME) / 1000);

    if(secondsElapased < 4){
        return 0.1
    }

    if(secondsElapased < 8){
        return 0.25
    }

    if(secondsElapased < 12){
        return 0.5
    }

    return 1
}

export const isFryer = (x: any): x is Fryer => x && x.isFryer
export const isFinishedFryer = (x: any): x is Fryer => isFryer(x) && x.finishedAt !== null && x.onFire === false

const isFryable = (x: any): x is Fryable => isFoodItem(x) && fryableIds.includes(x.foodItemId)

const isSpicyFryable = (x: any): x is Fryable => isFoodItem(x) && spicyFryableIds.includes(x.foodItemId)

function canHandleDrop(this: Fryer, object: GameObject): object is GameObject & HasHitBox {
    return hasHitbox(object)
        && isFryable(object)
        && (!this.isSpicy || isSpicyFryable(object))
        &&  this.content === null
}

function handleDropped(this: Fryer, droppedObject: Fryable, objects: GameObject[]){
    const fryer: Fryer = {
        ...this,
        isSpicy: this.isSpicy || droppedObject.foodItemId === 'spicy-raw-filet',
        content: droppedObject.foodItemId,
        progress: 0
    }

    objects = removeGameObjectById(objects, droppedObject)

    objects = updateGameObjectById(objects, fryer)

    return objects;
}

function retrieve(this: Fryer, objects: GameObject[]){
    if(this.content === null) throw Error('Cannot retreive from fryer when it has no content')

    const newFoodItem = createFoodItem(fryYield[this.content])

    let fryer = {
        ...this,
        content: null,
        finishedAt: null
    }

    objects = updateGameObjectById(objects, fryer);

    objects = [ ...objects, newFoodItem ]

    return [ objects, newFoodItem ] as [ GameObject[], FoodItem ]
}

export function fryerReducer(object: GameObject, state: GameState) {
    if (!isFryer(object) || state.isPaused) return state;

    let fryer = object as Fryer;

    if(fryer.content === null) {
        return state;
    }

    let objects = state.objects;

    let newProgress = (fryer.progress || 0) + 0.005;

    if(!fryer.finishedAt){
        if(newProgress < 1){
            fryer = {
                ...fryer,
                progress: newProgress
            }
        } else {
            fryer = {
                ...fryer,
                progress: null,
                finishedAt: state.frameNumber
            }
        }
    }

    const fryingSounds = AssetManager.loadAliasedAudio(`frying.${fryer.id}`, `frying.mp3`)
    const fireSounds = AssetManager.loadAliasedAudio(`fire.${fryer.id}`, `fire.mp3`)
    const beeping1 = AssetManager.loadAliasedAudio(`beeping.${fryer.id}.1`, 'beeping-1.mp3')
    const beeping2 = AssetManager.loadAliasedAudio(`beeping.${fryer.id}.2`, 'beeping-2.mp3')
    const beeping3 = AssetManager.loadAliasedAudio(`beeping.${fryer.id}.3`, 'beeping-3.mp3')

    if(fryer.finishedAt){
        const framesElapsed =  state.frameNumber -  fryer.finishedAt;
        const secondsElapased = ((framesElapsed * MS_PER_FRAME) / 1000);

        if(secondsElapased < 4){
            beeping1.play(0.15);
            fryingSounds.play(0.15);
        } else if(secondsElapased < 8){
            beeping2.play(0.15);
            fryingSounds.play(0.15);
        } else if(secondsElapased < 12){
            beeping3.play(0.15);
            fryingSounds.play(0.15);
        } else if(secondsElapased > 12){
            fireSounds.play(0.15);
            fryer = {
                ...fryer,
                onFire: true
            }
        }
        if(secondsElapased > 18){
            return {
                ...state,
                isPaused: true,
                canPause: false,
                objects: [
                    ...state.objects,
                    ...loadFinishedLevelMenu(state)
                ]
            }
        }
    } else {
        fryingSounds.play(0.15);
    }

    objects = updateGameObjectById(objects, fryer)

    return {
        ...state,
        objects
    }
}

export const fryYield : Record<FryableId, FoodItemId> = {
    'original-raw-filet': 'original-fried-filet',
    'spicy-raw-filet': 'spicy-fried-filet',
    'raw-breaded-nuggets': 'fried-nuggets',
    'potato-slices': 'waffle-fries'
}