import { coalesceX, Direction, Pixels } from "Dimensions";
import { GameObject } from "State";
import { getOverlappingObjects, hasHitbox, HasHitBox, Hitbox, overlaps } from "./HasHitbox";

export type CanFaceADirection = {
    facing: Direction;
}

export const createCanFaceADirection = (facing: Direction = 'south'): CanFaceADirection => ({
    facing
})

export const canFaceADirection = (x: any): x is CanFaceADirection => typeof x.facing !== 'undefined'

export function getObjectsInfrontOf<T extends GameObject>(target: CanFaceADirection & HasHitBox, objects: T[]) : T[] {
    const adjustedHitbox = getAreaInfrontOf(target)
    return getOverlappingObjects(adjustedHitbox, objects)
}

export function getObjectsFacing<T extends GameObject>(target: HasHitBox, objects: T[]) {
    const { hitbox } = target
    return objects.filter((object) => 
        canFaceADirection(object)  
        && hasHitbox(object)
        && overlaps(hitbox, getAreaInfrontOf(object))
    )
}

function getAreaInfrontOf({ hitbox, facing }: CanFaceADirection & HasHitBox) {
    const offset = offsets[facing];

    const adjustedHitbox: Hitbox = {
        x: hitbox.x + offset.x as Pixels,
        z: hitbox.z + offset.z as Pixels,
        y: hitbox.y,
        dx: hitbox.dx + offset.dx as Pixels,
        dz: hitbox.dz + offset.dz as Pixels,
    }

    return adjustedHitbox
}

const padding = coalesceX('1studs')
const offsets = {
    north: {
        x: +(padding/2),
        z: +padding,
        dx: -padding,
        dz: 0
    },
    south: {
        x: +(padding/2),
        z: -padding,
        dx: -padding,
        dz: 0
    },
    east: {
        x: +padding,
        z: +(padding),
        dx: 0,
        dz: -(padding*2)
    },
    west: {
        x: -padding,
        z: +(padding),
        dx: 0,
        dz: -(padding*2)
    },
}
