import { ControllerId, Controls } from "Controls";
import { coalesceX, coalesceY, Dimension, font, Pixels, WIDTH } from "Dimensions";
import { CharacterId, isCharacterUnlocked } from "Objects";
import { Animations, AssetManager, createTextRenderer, renderText } from "Renderer";
import { GameObject, GameState, updateGameObjectById, updateGameObjectsWhere } from "State";
import { createWithBoundingbox, createWithId, filter, HasBoundingbox, HasId, HasOverlay, HasRenderer } from "Traits";

export type CharacterMenu = HasId & HasRenderer & HasOverlay & {
    isCharacterMenu: true;
    selectedCharacters: Record<ControllerId, CharacterId | null>;
};

export const isCharacterMenu = (x: any): x is CharacterMenu => x && x.isCharacterMenu;

export const createCharacterMenu = (): CharacterMenu => ({
    ...createWithId(),
    render,
    isCharacterMenu: true,
    hasOverlay: true,
    selectedCharacters: {
        1: null,
        2: null,
        3: null,
        4: null
    },
})

type CharacterMatrix = CharacterId[][];

const characterMatrix: CharacterMatrix = [
    ['jeannette','hoho',  'mandi', 'josh', 'julie', 'miles', 'lydia'],
    ['truett',   'nonni', 'james', 'tori', 'charles', 'joy', 'isaac' ]
]

const controllerColors: Record<ControllerId, string> = {
    1: '#1455e8',
    2: '#21e113',
    3: '#edb22e ',
    4: '#d11519'
}

export function characterMenuReducer(object: GameObject, state: GameState) {
    if (!isCharacterMenu(object)) return state;

    let objects = state.objects;

    let menu = object as CharacterMenu;

    for(let controller of Controls){

        let anyArrowPressed = controller.justPressed('up')
            || controller.justPressed('down')
            || controller.justPressed('left')
            || controller.justPressed('right')

        if(anyArrowPressed && state.selectedCharacters[controller.id] === null){
            let newCharacterId: CharacterId;
            let selectedCharacterIds = Object.values(menu.selectedCharacters);

            let rowIndex, columnIndex, axis, direction;

            if(menu.selectedCharacters[controller.id] === null){
                rowIndex = 0;
                columnIndex = -1;
                axis = 'horizonal';
                direction = +1;
            } else {
                let currentCharacterIdForPlayer = menu.selectedCharacters[controller.id] as CharacterId;
                rowIndex = characterMatrix.findIndex( row => row.includes(currentCharacterIdForPlayer))
                columnIndex = characterMatrix[rowIndex].indexOf(currentCharacterIdForPlayer)
                axis = controller.justPressed('up') || controller.justPressed('down') ? 'vertical' : 'horizonal'
                direction = controller.justPressed('up') || controller.justPressed('left') ? -1 : +1;
            }


            do {
                let formerRowIndex: number = rowIndex;
                let formerColumnIndex: number = columnIndex;
                if(axis === 'vertical') rowIndex = characterMatrix[rowIndex + direction]?.[columnIndex] ? rowIndex + direction : rowIndex;
                else columnIndex = characterMatrix[rowIndex]?.[columnIndex + direction] ? columnIndex + direction : columnIndex;

                newCharacterId = characterMatrix[rowIndex][columnIndex];

                if(formerColumnIndex === columnIndex && formerRowIndex === rowIndex && selectedCharacterIds.includes(newCharacterId)){
                    newCharacterId = menu.selectedCharacters[controller.id] as CharacterId;
                    break;
                }

            } while(selectedCharacterIds.includes(newCharacterId) && menu.selectedCharacters[controller.id] !== newCharacterId)

            menu = {
                ...menu,
                selectedCharacters: {
                    ...menu.selectedCharacters,
                    [controller.id]: newCharacterId
                }
            }
        }

        let playersSelectedCharacter = menu.selectedCharacters[controller.id];

        if(controller.justPressed('primaryAction') && playersSelectedCharacter && isCharacterUnlocked(state, playersSelectedCharacter)){
            state = {
                ...state,
                selectedCharacters: {
                    ...state.selectedCharacters,
                    [controller.id]: playersSelectedCharacter
                }
            }
        }

        if(controller.justPressed('secondaryAction')){
            state = {
                ...state,
                selectedCharacters: {
                    ...state.selectedCharacters,
                    [controller.id]: null
                }
            }
        }

    }

    objects = updateGameObjectById(objects, menu);

    return {
        ...state,
        objects
    };
}


function render(state: GameState, object: GameObject, ctx: CanvasRenderingContext2D) {
    let menu = object as CharacterMenu;
    const menuWidth = coalesceX('80%');
    const maxCharacterPerRow = Math.max(...characterMatrix.map(row => row.length))

    const topMargin = coalesceY('20%');
    const leftMargin = coalesceX('10%');
    const menuHeight = coalesceY('80%');

    if(Object.values(state.selectedCharacters).some( selection => selection !== null)){
        renderText(ctx, state, coalesceX('90%'), coalesceY('5%'), {
            text: 'Press Start When Ready',
            align: 'right',
            baseline: 'top',
            font: `${font.small}px apercu-medium`,
            color: '#e60e33',
            fade: {
                startAt: 25 * 2,
                duration: 1000,
                loop: true
            }
        })
    }

    characterMatrix.forEach((row, yIndex) => {
        row.forEach((characterId, xIndex) => {

            const [ controllerId ] = (
                Object.entries(menu.selectedCharacters)
                    .find(([ controllerId, selectedCharacterId ]) => characterId === selectedCharacterId)
                || []
            )

            const isSelected = controllerId && state.selectedCharacters[Number(controllerId) as ControllerId] === characterId

            renderCharacterIcon(ctx, {
                characterId,
                locked: !isCharacterUnlocked(state, characterId),
                sideLength: menuWidth / maxCharacterPerRow,
                y: topMargin + (yIndex*(menuWidth / maxCharacterPerRow + 30)),
                x: leftMargin + (xIndex*(menuWidth / maxCharacterPerRow)),
                outlineColor: controllerId !== undefined ? controllerColors[Number(controllerId) as ControllerId] : undefined,
                outlineWidth: isSelected ? 8 : 4
            })
        })
    })
}

type RenderCharacterIconOptions = {
    x: number,
    y: number,
    sideLength: number,
    characterId: CharacterId,
    locked?: boolean
    outlineColor?: string
    outlineWidth?: number
}

function renderCharacterIcon(ctx: CanvasRenderingContext2D, { characterId, sideLength, x, y, outlineColor, outlineWidth = 4, locked  }: RenderCharacterIconOptions){
    ctx.save();
    ctx.beginPath();
    ctx.arc(x + sideLength/2, y + sideLength/2, sideLength/2, 0, Math.PI * 2, true);
    ctx.closePath();
    ctx.clip();

    ctx.drawImage(
        AssetManager.getImage(`characters/${characterId}/${locked ? 'locked' : 'profile'}/1.png`),
        x, y,
        sideLength, sideLength
    )

    ctx.beginPath();
    ctx.arc(x, y, sideLength/2, 0, Math.PI * 2, true);
    ctx.clip();
    ctx.closePath();
    ctx.restore();

    ctx.fillStyle = locked ? 'black' : '#e60e33'
    ctx.font = `${font.small}px apercu-medium`
    ctx.textAlign = 'center'
    ctx.textBaseline = 'top'
    ctx.fillText(locked ? 'locked' : characterId, x + sideLength/2, y + sideLength + 4)

    if(outlineColor !== undefined){
        ctx.beginPath();
        ctx.strokeStyle = outlineColor
        ctx.lineWidth = outlineWidth
        ctx.arc(x + sideLength/2, y + sideLength/2, sideLength/2, 0, Math.PI * 2, true);
        ctx.stroke();
    }
}
