import { Dimension } from "Dimensions";
import { combineRenderers, create3DRenderer, createContentRenderer, createProgressRenderer } from "Renderer";
import { GameObject, removeGameObjectById, updateGameObjectById } from "State";
import { createWithHitbox, createWithId, HasRenderer, HasHitBox, HasId, CanHandleDrop, hasHitbox } from "Traits";
import { Character } from "./Character";
import { createFoodItem, FoodItem, FoodItemId, isFoodItem } from "./FoodItem";

export type CuttingBoard = HasId & HasHitBox & HasRenderer & CanHandleDrop & {
    isCuttingBoard: true,
    content: null | ChoppableId,
    progress: null | number,
    canChop: () => boolean,
    chop: (chopper: Character, objects: GameObject[]) => GameObject[]
};

export const isCuttingBoard = (x: any): x is CuttingBoard => x && x.isCuttingBoard

export const createCuttingBoard = (x?: Dimension, z?: Dimension): CuttingBoard => ({
    ...createWithId(),
    isCuttingBoard: true,
    content: null,
    progress: null,
    ...createWithHitbox('2studs', '2studs', x, z),
    render: combineRenderers(
        create3DRenderer('cutting-board'),
        createContentRenderer({ sprite: 'cutting-board', content: (cuttingBoard: CuttingBoard) => cuttingBoard.content }),
        createProgressRenderer('cutting-board')
    ),
    canHandleDrop,
    handleDropped,
    canChop,
    chop
})

type ChoppableId = 'lettuce-head' | 'raw-chicken-breast' | 'tomato' | 'potato' | 'apple' | 'strawberries';

const choppableIds = [ 'lettuce-head','raw-chicken-breast', 'tomato', 'potato', 'apple', 'strawberries' ]

export type Choppable = FoodItem & {
    foodItemId: ChoppableId
}

export const isChoppable = (x: any): x is Choppable => isFoodItem(x) && choppableIds.includes(x.foodItemId)

function canHandleDrop(this: CuttingBoard, object: GameObject): object is GameObject & HasHitBox {
    return hasHitbox(object) && isChoppable(object) &&  this.content === null
}

function handleDropped(this: CuttingBoard, droppedObject: Choppable, objects: GameObject[]){
    const cuttingBoard: CuttingBoard = {
        ...this,
        content: droppedObject.foodItemId,
        progress: 0
    }

    objects = removeGameObjectById(objects, droppedObject)

    objects = updateGameObjectById(objects, cuttingBoard)

    return objects;
}

function canChop(this: CuttingBoard){
    return this.content !== null
}

function chop(this: CuttingBoard, chopper: Character, objects: GameObject[]){
    if(this.content === null) return objects

    let newProgress = (this.progress || 0) + 0.01;

    let cuttingBoard: CuttingBoard;

    if(newProgress < 1){
        cuttingBoard = {
            ...this,
            progress: newProgress
        }
    } else {
        const newFoodItem = createFoodItem(chopYield[this.content])

        cuttingBoard = {
            ...this,
            progress: null,
            content: null
        }

        objects = [ ...objects, newFoodItem ]

        objects = newFoodItem.handlePickedUpBy(chopper, objects)

        objects = updateGameObjectById(objects, {
            ...chopper,
            carrying: newFoodItem.id
        })

    }

    objects = updateGameObjectById(objects, cuttingBoard)

    return objects
}

export const chopYield : Record<ChoppableId, FoodItemId> = {
    'lettuce-head': 'lettuce-leaf',
    'raw-chicken-breast':'raw-nuggets',
    'tomato': 'tomato-slice',
    'potato': 'potato-slices',
    'apple': 'diced-apples',
    'strawberries': 'diced-strawberries'
}