import { ControllerId, Controls } from "Controls"
import { coalesceX, coalesceZ, createVector, Direction, GAME_HEIGHT_STUDS, GAME_WIDTH_STUDS } from "Dimensions";
import { GameState, GameObject, updateGameObjectById } from "State";
import { canBlockMovement } from "./CanBlockMovement";
import { CanFaceADirection, createCanFaceADirection } from "./CanFaceADirection";
import { hasCollision, HasHitBox, moveObject } from "./HasHitbox";

export type CanBeControlled = CanFaceADirection & {
    controllerId: ControllerId;
    facing: Direction;
    moving: boolean;
    startedDashAt: null | number;
}

export const createCanBeControlled = (controllerId: ControllerId, facing: Direction = 'south'): CanBeControlled => ({
    ...createCanFaceADirection(facing),
    controllerId,
    moving: false,
    startedDashAt: null
})

export const canBeControlled = (x: any): x is CanBeControlled => typeof x.controllerId !== 'undefined'

export function canBeControlledReducer(object: GameObject, state: GameState): GameState {
    if(!canBeControlled(object) || state.isPaused) return state;

    const controller = Controls[object.controllerId];

    let facing: Direction;
    if(controller.isPressed('up')){
        facing = 'north';
    } else if(controller.isPressed('down')){
       facing = 'south';
    } else if(controller.isPressed('left')){
        facing = 'west';
    } else if(controller.isPressed('right')){
        facing = 'east';
    } else {
        facing = object.facing;
    }

    let moving = controller.isPressed('up') || controller.isPressed('down') || controller.isPressed('left') || controller.isPressed('right');

    object = {
        ...object,
        facing,
        moving,
    }

    let speed = '0.25studs';

    if(controller.justPressed('bumper')){
        object = {
            ...object,
            startedDashAt: state.frameNumber
        }
    }

    if(object.startedDashAt && object.startedDashAt > state.frameNumber - 3.5){
        speed = '1studs'
    }

    if (object.moving) {
        const movedObject = moveObject(object, createVector(speed, object.facing));
        const blockers = state.objects.filter(canBlockMovement);

        if(!hasCollision(movedObject, blockers) && !outsidePlayArea(movedObject)) {
            object = movedObject
        }
    }

    return {
        ...state,
        objects: updateGameObjectById(state.objects, object)
    };
}

function outsidePlayArea(object: HasHitBox){

    if(object.hitbox.x < 0) return true;
    if(object.hitbox.x + object.hitbox.dx > coalesceX(`${GAME_WIDTH_STUDS}studs`)) return true;
    if(object.hitbox.z < 0) return true;
    if(object.hitbox.z + object.hitbox.dz > coalesceZ(`${GAME_HEIGHT_STUDS}studs`)) return true;

    return false
}