
export type ControlName = 'down' | 'up' | 'left' | 'right' | 'primaryAction' | 'secondaryAction' | 'tertiaryAction' | 'start' | 'bumper' | 'minus'

export type ControllerId = 1 | 2 | 3 | 4;

export type ControllerData = Record<ControlName, boolean>;

class Controller {

    down: boolean = false
    up: boolean = false
    left: boolean = false
    right: boolean = false
    primaryAction: boolean = false
    secondaryAction: boolean = false
    tertiaryAction: boolean = false
    start: boolean = false
    bumper: boolean = false
    minus: boolean = false;

    id: ControllerId;

    constructor(id: ControllerId){
        this.id = id
    }

    isPressed(control: ControlName){
        return this[control]
    }

    justPressed(control: ControlName){
        return this[control] && this.lastInput && !this.lastInput[control]
    }

    justReleased(control: ControlName){
        return !this[control] && this.lastInput && this.lastInput[control]
    }

    lastInput: ControllerData | null = null

    recordInput(){
        this.lastInput = { ...this }
        const gamepadIndex = this.id - 1;
        const gamepadInput = getGamePadInput(gamepadIndex);
        if(gamepadInput) Object.assign(this, gamepadInput);
    }
}

export const Controls  = {
    1: new Controller(1),
    2: new Controller(2),
    3: new Controller(3),
    4: new Controller(4),
    [Symbol.iterator]: function*(){
        yield Controls[1];
        yield Controls[2];
        yield Controls[3];
        yield Controls[4];
    }
}

export function recordInputs(){
    for(let controllerId of [1, 2, 3, 4]){
        Controls[controllerId as ControllerId].recordInput();
    }
}

function getGamePadInput(gamepadIndex: number): false | ControllerData {
    const gamepads = navigator.getGamepads();
    const gamepad = gamepads[gamepadIndex];
    if(!gamepad){
        return false;
    }
    return {
        up: gamepad.axes[1] === -1,
        down: gamepad.axes[1] === 1,
        left: gamepad.axes[0] === -1,
        right: gamepad.axes[0] === 1,
        primaryAction: gamepad.buttons[1].pressed === true,
        secondaryAction: gamepad.buttons[0].pressed === true || gamepad.buttons[2].pressed === true,
        tertiaryAction: gamepad.buttons[3].pressed === true,
        start: gamepad.buttons[9].pressed === true,
        bumper: gamepad.buttons[4].pressed === true || gamepad.buttons[5].pressed === true,
        minus: gamepad.buttons[8].pressed === true
    }
}


const keyboardMap: Record<string, [ 1 | 2, ControlName ]> = {
    'ArrowDown':    [ 1, 'down' ],
    'ArrowUp':      [ 1, 'up' ],
    'ArrowLeft':    [ 1, 'left' ],
    'ArrowRight':   [ 1, 'right' ],
    ' ':            [ 1, 'primaryAction' ],
    'Meta':         [ 1, 'secondaryAction' ],
    'Shift':        [ 1, 'bumper' ],
    'Escape':       [ 1, 'minus' ],
    's':            [ 1, 'start' ],
    'h':            [ 1, 'tertiaryAction' ]
}

function handleKeyDown(e: KeyboardEvent) {
    e.preventDefault();
    if (e.repeat) return;
    recordInputs();
    if(e.key in keyboardMap){
        const [ controllerId, control ] = keyboardMap[e.key as keyof typeof keyboardMap];
        Controls[controllerId][control] = true;
    }
}

function handleKeyUp(e: KeyboardEvent) {
    e.preventDefault();
    if (e.repeat) return;
    recordInputs();
    if(e.key in keyboardMap){
        const [ controllerId, control ] = keyboardMap[e.key as keyof typeof keyboardMap];
        Controls[controllerId][control] = false;
    }
}

window.addEventListener('keydown', handleKeyDown);
window.addEventListener('keyup', handleKeyUp)