import { Level } from "Levels";
import { Character, createFloor, createOrderBoard, createScoreCounter, createTimer, FoodItemId, OrderTimeline, randomElement } from "Objects";
import { GameState } from "State";
import { appl, bbqS, bflo, blbr, bTb1, bTb2, buns, cBrd, cfaS, chic, fry1, fry2, fry3, hnyM, ltce, orng, pikl, piUp, poly, prJk, ptat, rnch, srir, sTb1, sTb2, strb, Tile, tileLevel, tmto, trsh, trSt, usaC, wall } from "./utils";



const map = (cha1: Tile, cha2: Tile, cha3: Tile, cha4: Tile) => {
    let numberOfPlayers = [ cha1, cha2, cha3, cha4].filter( x => x != null).length
    const wlop = numberOfPlayers > 2 ? wall : null
    return ([
        [trsh, appl, strb, blbr, orng, buns, usaC, prJk, wall, wall, piUp, piUp, bTb1, bTb1, sTb1, sTb1, chic, cBrd, wall],
        [null, null, null, null, null, null, cha1, cha2, null, null, null, null, null, null, null, null, null, null, null, null],
        [null, null, null, null, null, null, cha3, cha4, null, null, null, null, null, null, null, null, null, null, null, null],
        [cBrd, wall, cBrd, wall, wall, trSt, null, null, trSt, cfaS, hnyM, poly, null, null, bbqS, bflo, srir, rnch, null, null],
        [null, null, null, null, null, null, null, null, null, trsh, fry1, fry1, wlop, wlop, fry2, fry2, fry3, fry3, null, null],
        [null, null, null, null, null, null, null, null, null, wall, null, null, null, null, null, null, null, null, null, null],
        [trsh, wall, ltce, tmto, pikl, ptat, null, null, null, wall, null, null, null, null, null, null, bTb1, bTb1, null, null],
    ])
}

export const TrulyAPleasure: Level = {
    id: 5,
    name: 'Truly A Pleasure',
    targetScores: [4, 7, 10],
    tutorialEntries: [],
    getObjects(state: GameState) {
        return [
            createFloor(),
            createOrderBoard(orders, state),
            createTimer(state.frameNumber, 180 * 1000 / (1000 / 25)),
            createScoreCounter(),
            ...tileLevel(map, state)
        ]
    }
}

const getEntree = () => randomElement<FoodItemId>([
    'fried-nuggets',
    'original-sandwich',
    'original-sandwich-no-pickles',
    'deluxe-sandwich',
    'deluxe-sandwich-no-pickles',
    'spicy-sandwich',
    'spicy-sandwich-no-pickles',
    'spicy-deluxe-sandwich',
    'spicy-deluxe-sandwich-no-pickles'
])

const getSide = () => randomElement<FoodItemId>([
    'fruit-cup',
    'waffle-fries'
])

const getSauce = () => randomElement<FoodItemId>(['cfa-sauce', 'bbq-sauce', 'poly-sauce', 'ranch-sauce', 'buffalo-sauce', 'sriracha-sauce', 'hny-mstrd-sauce'])

const getEntreeWSauce = (): FoodItemId[] => [getEntree(), getSauce()]

const getMeal = (): FoodItemId[] => [getEntree(), getSide()]

const getEntreeOnly = (): FoodItemId[] => [getEntree()]

const getFryWSauce = (): FoodItemId[] => ['waffle-fries', getSauce()]

const orders: OrderTimeline = [
    [0, getMeal],
    [5, getEntreeWSauce],
    [10, getFryWSauce],
    [30, getMeal],
    [25, getEntreeOnly],
    [30, getMeal],
    [30, getMeal],
    [25, getEntreeOnly],
    [30, getMeal],
    [30, getMeal],
    [25, getEntreeOnly],
    [30, getMeal],
]