import { WIDTH, coalesceX, HEIGHT, coalesceZ, GAME_HEIGHT_STUDS } from "Dimensions";
import { GameState } from "State";
import { createWithId, HasId, HasRenderer } from "Traits";

export type Floor = HasId & HasRenderer;

const floorWidth = WIDTH;
const floorHeight = coalesceZ(`${GAME_HEIGHT_STUDS}studs`)

export const createFloor = (): Floor => ({
    ...createWithId(),
    render: (_: GameState, state: Floor, ctx: CanvasRenderingContext2D) => {
        ctx.fillStyle = "#9c9a9e";
        ctx.fillRect(0, HEIGHT - floorHeight, floorWidth, floorHeight);
        ctx.fillStyle = '#bfa186';
        ctx.fillRect(0, 0, floorWidth, HEIGHT - floorHeight);
        ctx.strokeStyle = 'black';
        for(let cursorX = 0; cursorX <= floorWidth; cursorX += coalesceX('2studs')){
            ctx.beginPath();
            ctx.moveTo(cursorX, 0);
            ctx.lineTo(cursorX, HEIGHT);
            ctx.stroke();
        }

        for(let cursorY = HEIGHT; cursorY > 0; cursorY -= coalesceX('2studs')){
            ctx.beginPath();
            ctx.moveTo(0, cursorY);
            ctx.lineTo(floorWidth, cursorY);
            ctx.stroke();
        }
    }
})