import { CuttingBoard } from "Objects";
import { Animations, Create3DRendererOptions, getSpriteCoords, process3DRendererOptions } from "Renderer";
import { GameObject, GameState } from "State";
import { HasHitBox, Renderer } from "Traits";

type HasProgress = {
    progress: null | number
}

export const createProgressRenderer = (options: Create3DRendererOptions<GameObject & HasHitBox & HasProgress>): Renderer => {
    const renderer = (state: GameState, object: GameObject & HasHitBox & HasProgress, ctx: CanvasRenderingContext2D) => {
        const { spriteKey } = process3DRendererOptions(options, object, state)

        if(spriteKey === null) return;

        const objectAnimation = Animations[spriteKey];

        const [ objectX, objectY, objectWidth, objectHeight ] = getSpriteCoords(object.hitbox, objectAnimation.size);

        if(object.progress !== null) {
            ctx.beginPath();
            ctx.lineWidth = 5
            ctx.strokeStyle = 'white';
            ctx.moveTo(objectX, objectY);
            ctx.lineTo(objectX + objectWidth, objectY);
            ctx.stroke();

            ctx.beginPath();
            ctx.strokeStyle = '#5e9b4a';
            ctx.moveTo(objectX, objectY);
            ctx.lineTo(objectX + (objectWidth * object.progress), objectY);
            ctx.stroke();
        }

    }

    return renderer as Renderer;
}
