import { Level } from "Levels";
import { Character, createFloor, createOrderBoard, createScoreCounter, createTimer, FoodItemId, OrderTimeline, randomElement } from "Objects";
import type { GameObject, GameState } from "State";
import { bTb1, bTb2, buns, cBrd, chic, fry1, fry2, fry3, ltce, pikl, piUp, ptat, Tile, tileLevel, tmto, trsh, trSt, usaC, wall } from "./utils";

const map = (cha1: Tile, cha2: Tile, cha3: Tile, cha4: Tile) => ([
    [wall, piUp, piUp, wall, fry3, fry3, cBrd, ptat, wall, wall, wall, wall, wall, wall, wall, wall, wall, wall, wall, wall ],
    [null, null, null, null, null, null, null, null, null, null, null, null, wall, fry1, fry1, null, fry2, fry2, null, null ],
    [null, cha1, cha2, null, null, null, null, null, null, null, null, null, wall, null, null, null, null, null, null, null ],
    [null, cha3, cha4, null, pikl, buns, wall, usaC, cBrd, ltce, tmto, trSt, wall, null, null, null, null, null, chic, null ],
    [null, null, null, null, null, null, null, null, null, null, null, null, wall, null, null, null, null, null, chic, null ],
    [null, null, null, null, null, null, null, null, null, null, null, null, null, null, null, null, null, null, trsh, null ],
    [null, null, null, null, null, null, null, null, null, null, null, null, null, bTb1, bTb1, null, bTb2, bTb2, null, null ],
])

export const HowWillWeTopThis: Level = {
    id: 2,
    name: 'How Will We Top This?',
    targetScores: [ 5, 10, 15 ],
    tutorialEntries: ['deluxe-sandwich'],
    getObjects(state: GameState){
        return [
            createFloor(),
            createOrderBoard(orders, state),
            createTimer(state.frameNumber, 180 * 1000 / (1000/25)),
            createScoreCounter(),
            ...tileLevel(map, state)
        ]
    }
}

const getEntree = () => randomElement<FoodItemId>([ 'deluxe-sandwich', 'original-sandwich', 'original-sandwich-no-pickles', 'deluxe-sandwich-no-pickles'])

const getMeal = (): FoodItemId[] => [ getEntree(), 'waffle-fries' ]

const getEntreeOnly = (): FoodItemId[] => [ getEntree() ]

const orders : OrderTimeline = [
    [0, () => ['original-sandwich']],
    [5, () => ['deluxe-sandwich']],
    [25, getMeal],
    [15, getEntreeOnly],
    [15, getMeal],
    [20, getEntreeOnly],
    [15, getMeal],
    [20, getMeal],
    [10, getMeal],
    [20, getEntreeOnly],
    [15, getMeal],
    [20, getMeal],
    [5, () => ['original-sandwich']],
    [5, getEntreeOnly],
    [15, getMeal],
    [5, getMeal],
    [5, getMeal]
]