import { ControllerId, Controls } from "Controls";
import { coalesceX, coalesceY, Dimension, font } from "Dimensions";
import {  create2DRenderer, createTextRenderer, CreateTextRendererOptions } from "Renderer";
import { MS_PER_FRAME } from "startGame";
import { GameObject, GameState } from "State";
import {  createWithBoundingbox, createWithId, createWithOrigin, HasBoundingbox, HasId, HasOrigin, HasOverlay, HasRenderer } from "Traits";
import { loadLevelMenu } from "./loadLevelMenu";

export type GameText = HasId & HasOrigin & HasOverlay & HasRenderer;

export const createGameText = (options: CreateTextRendererOptions<GameText>, x: Dimension = 0, y: Dimension = 0, blink?: boolean): GameText => ({
    ...createWithId(),
    ...createWithOrigin(x, y),
    hasOverlay: true,
    render: createTextRenderer({
        text: typeof options === 'string' ? options : '',
        font: `${font.medium}px apercu-medium`,
        color: '#e60e33',
        fade: blink ? {
            startAt: 25 * 2,
            duration: 1000,
            loop: true
        } : undefined,
        ...(typeof options === 'object' ? options : {})
    })
})