import { Dimension } from "Dimensions";
import { combineRenderers, create3DRenderer, createContentRenderer } from "Renderer";
import { CanBlockMovement, createCanBlockMovement, createWithCounterSpace, createWithHitbox, createWithId, HasRenderer, HasCounterSpace, HasHitBox, HasId, CanHandleDrop, hasHitbox, CanProvide, createCanProvide } from "Traits";
import { createFoodItem, FoodItemId } from "./FoodItem";
import { createTray } from "./Tray";

export type TrayStack = HasId & HasHitBox & CanProvide & HasRenderer;

export const createTrayStack = (x?: Dimension, z?: Dimension): TrayStack => ({
    ...createWithId(),
    ...createWithHitbox('2studs', '2studs', x, z, '1studs'),
    ...createCanProvide(() => createTray()),
    render: create3DRenderer('tray-stack'),
})

