import { coalesceY } from "Dimensions"
import { GameObject, updateGameObjectById } from "State"
import { CanHandleDrop } from "./CanHandleDrop"
import { hasHitbox, HasHitBox } from "./HasHitbox"

export type HasCounterSpace =  CanHandleDrop & {
    counterSpaceTakenBy: number | null
}

export const createWithCounterSpace = (): HasCounterSpace => ({
    counterSpaceTakenBy: null,
    canHandleDrop,
    handleDropped
})

export const hasCounterSpace = (x: any): x is HasCounterSpace => x.counterSpaceTakenBy !== undefined

export const counterSpaceContains = (object: GameObject) => (x: any) => hasCounterSpace(x) && x.counterSpaceTakenBy === object.id

function canHandleDrop(this: HasCounterSpace, object: GameObject) : object is GameObject & HasHitBox {
    return this.counterSpaceTakenBy === null && hasHitbox(object)
}

function handleDropped(this: GameObject & HasCounterSpace & HasHitBox, droppedObject: GameObject & HasHitBox, objects: GameObject[]){
    const counterSpace: GameObject & HasCounterSpace = {
        ...this,
        counterSpaceTakenBy: droppedObject.id
    }
    
    objects = updateGameObjectById(objects, counterSpace);

    objects = updateGameObjectById(objects, {
        ...droppedObject,
        hitbox: {
            ...this.hitbox,
            y: coalesceY('1.1studs')
        }
    })

    return objects;
}