import { Level } from "Levels";
import { Character, createFloor, createOrderBoard, createScoreCounter, createTimer, FoodItemId, OrderTimeline, randomElement } from "Objects";
import { GameState } from "State";
import { appl, bbqS, bflo, blbr, bTb1, bTb2, buns, cBrd, cfaS, chic, fry1, fry2, fry3, hnyM, ltce, orng, pikl, piUp, poly, ptat, rnch, srir, strb, Tile, tileLevel, tmto, trsh, trSt, usaC, wall } from "./utils";

const map = (cha1: Tile, cha2: Tile, cha3: Tile, cha4: Tile) => ([
    [fry1, fry1, fry2, fry2, fry3, fry3, bTb1, bTb1, bTb2, bTb2, cBrd, chic, buns, wall, wall, wall, wall, wall, piUp, piUp ],
    [null, null, null, null, null, null, null, null, null, null, null, null, null, null, null, null, null, null, null, null ],
    [null, null, null, null, null, null, null, null, null, null, null, null, null, null, cha1, cha2, null, null, null, null ],
    [wall, wall, wall, wall, wall, wall, wall, wall, wall, wall, trSt, wall, wall, null, cha3, cha4, null, null, null, bbqS ],
    [null, null, null, null, null, null, null, null, null, null, null, null, null, null, null, null, hnyM, null, null, bflo ],
    [null, null, null, null, null, null, null, null, null, null, null, null, null, null, null, null, poly, null, null, rnch ],
    [wall, wall, wall, cBrd, ptat, strb, appl, blbr, orng, pikl, cBrd, null, null, null, null, null, cfaS, null, null, srir ],
])

export const LetsGetSaucy : Level = {
    id: 3,
    name: 'Let\'s Get Saucy',
    targetScores: [ 5, 10, 15 ],
    tutorialEntries: [ 'fried-nuggets', 'fruit-cup' ],
    getObjects(state: GameState){
        return [
            createFloor(),
            createOrderBoard(orders, state),
            createTimer(state.frameNumber, 180 * 1000 / (1000/25)),
            createScoreCounter(),
            ...tileLevel(map, state)
        ]
    }
}

const getSauce = () => randomElement<FoodItemId>(['cfa-sauce', 'bbq-sauce', 'poly-sauce', 'ranch-sauce', 'buffalo-sauce', 'sriracha-sauce', 'hny-mstrd-sauce'])

const getEntree = () => randomElement<FoodItemId>([ 'original-sandwich', 'fried-nuggets' ])

const getSide = () => randomElement<FoodItemId>([ 'waffle-fries', 'fruit-cup' ])

const getMeal = (): FoodItemId[] => [ getEntree(), getSide() ]

const getSideOnly = (): FoodItemId[] => [ getSide() ]

const getEntreeWSauce = (): FoodItemId[] => [ getEntree(), getSauce() ]

const getMealWSauce = (): FoodItemId[] => [ getEntree(), getSide(), getSauce() ]

const orders : OrderTimeline = [
    [0, () => ['fried-nuggets', 'cfa-sauce']],
    [5, () => ['fruit-cup']],
    [25, getMealWSauce],
    [15, getEntreeWSauce],
    [15, getMeal],
    [5, getSideOnly],
    [15, getMealWSauce],
    [15, getEntreeWSauce],
    [15, getMeal],
    [15, getEntreeWSauce],
    [5, getSideOnly],
    [5, getSideOnly],
    [20, getMealWSauce],
    [15, getEntreeWSauce],
    [15, getMeal],
    [20, getMealWSauce],
    [5, () => ['fried-nuggets']],
    [5, getEntreeWSauce],
    [5, getMealWSauce]
]