import { coalesceX, coalesceY, Dimension } from "Dimensions";
import { MS_PER_FRAME } from "startGame";
import { GameObject, GameState } from "State";
import { createWithBoundingbox, createWithId, HasBoundingbox, HasId, HasOverlay, HasRenderer } from "Traits";


export type StarDisplay = HasId & HasBoundingbox & HasRenderer & HasOverlay & {
    score: number;
    fadeInFrom: number | null;
    labels: string[];
};

export const createStarDisplay = (score: 0 | 1 | 2 | 3, width: Dimension, x: Dimension = 0, y: Dimension = 0, fadeInFrom: number | null = null, labels: string[] = []): StarDisplay => ({
    ...createWithId(),
    ...createWithBoundingbox(width, (coalesceX(width) / 3), x, y),
    hasOverlay: true,
    render,
    score,
    fadeInFrom,
    labels
})

export function render(state: GameState, object: GameObject, ctx: CanvasRenderingContext2D) {
    const starDisplay = object as StarDisplay;

    const { score, labels } = starDisplay;
    const { width, x, y } = starDisplay.boundingbox;

    let alpha1 = 1, alpha2 = 1, alpha3 = 1;

    if(starDisplay.fadeInFrom !== null){
        const startAt = starDisplay.fadeInFrom
        const duration = 1000;
        const elapsed = Date.now() - startAt;
        if(elapsed < 0){
            alpha1 = 0;
            alpha2 = 0;
            alpha3 = 0;
        } else {
            alpha1 = elapsed / duration
            alpha2 = (elapsed - duration)  / duration
            alpha3 = (elapsed - duration * 2)  / duration
        }
    }

    let color1 = score > 0 ? `rgba(250, 198, 42, ${alpha1})` : 'rgba(194, 189, 174, 1)';
    let color2 = score > 1 ? `rgba(250, 198, 42, ${alpha2})` : 'rgba(194, 189, 174, 1)';
    let color3 = score > 2 ? `rgba(250, 198, 42, ${alpha3})` : 'rgba(194, 189, 174, 1)';

    renderStar(ctx, width/3, x - width*.4, y, color1, labels[0])
    renderStar(ctx, width/3, x, y, color2, labels[1])
    renderStar(ctx, width/3, x + width*.4, y, color3, labels[2])
}

function renderStar(ctx: CanvasRenderingContext2D, sideLength: Dimension, x: Dimension, y: Dimension, color: string, label?: string) {
    const outerRadius = coalesceX(sideLength) / 2
    const innerRadius = outerRadius / 2

    let rot: number = Math.PI / 2 * 3;
    const cx = coalesceX(x);
    const cy = coalesceY(y);
    x = cx;

    y = cy;
    const step = Math.PI / 5;

    ctx.beginPath();
    ctx.moveTo(cx, cy - outerRadius)
    for (let i = 0; i < 5; i++) {
        x = cx + Math.cos(rot) * outerRadius;
        y = cy + Math.sin(rot) * outerRadius;
        ctx.lineTo(x, y)
        rot += step

        x = cx + Math.cos(rot) * innerRadius;
        y = cy + Math.sin(rot) * innerRadius;
        ctx.lineTo(x, y)
        rot += step
    }
    ctx.lineTo(cx, cy - outerRadius);
    ctx.closePath();
    ctx.lineWidth = 5;
    ctx.strokeStyle = color;
    ctx.stroke();
    ctx.fillStyle = color;
    ctx.fill();

    if(label){
        ctx.textAlign = 'center';
        ctx.fillText(label, cx, cy + outerRadius + 10)
    }
}