import { coalesceX } from "Dimensions";
import { Animations, AssetManager } from "Renderer";
import { renderFoodItemsInArea } from "Renderer/renderFoodItemsInArea";
import { MS_PER_FRAME } from "startGame";
import { GameObject, GameState, updateGameObjectById } from "State";
import { createHasOverlay, createWithId, HasId, HasOverlay, HasRenderer, Renderer } from "Traits";
import { FoodItemId } from "./FoodItem";



export type Order = {
    foodItems: FoodItemId[];
    placedAt: number;
    fullfilledAt: number | null;
}

export type OrderTimeline = [number, (() => FoodItemId[])][]

export type OrderBoard = HasId & HasRenderer & HasOverlay & {
    isOrderBoard: true;
    orders: Order[];
    fullfilled: (Order & { fullfilledAt: number })[];
    upcomingOrders: OrderTimeline;
    timestampForNextOrder: number;
    firstDeadline: number;
    secondDeadline: number;
}

export const isOrderBoard = (x: any): x is OrderBoard => x && x.isOrderBoard

const numberOfPlayers = (state: GameState) => Object.values(state.selectedCharacters).filter( x => x !== null).length

const firstDeadlines = {
    1: 60,
    2: 45,
    3: 30,
    4: 20
}

const secondDeadlines = {
    1: 120,
    2: 90,
    3: 60,
    4: 40
}

export const createOrderBoard = (upcomingOrders: OrderTimeline, state: GameState): OrderBoard => ({
    ...createWithId(),
    ...createHasOverlay(),
    isOrderBoard: true,
    orders: [],
    fullfilled: [],
    upcomingOrders,
    timestampForNextOrder: upcomingOrders[0]?.[0],
    firstDeadline: firstDeadlines[numberOfPlayers(state) as 1 | 2 | 3 | 4],
    secondDeadline: secondDeadlines[numberOfPlayers(state) as 1 | 2 | 3 | 4],
    render
})

const sideLength = coalesceX('3studs')

const render = ((state: GameState, board: OrderBoard, ctx: CanvasRenderingContext2D) => {
    ctx.beginPath();
    ctx.fillStyle = 'rgba(0, 0, 0, 0.5)';
    //@ts-ignore
    ctx.roundRect(0, 0, sideLength * board.orders.length, sideLength, 5);
    ctx.fill();

    let index = 0;
    for (let order of board.orders) {

        const framesElapsed = state.frameNumber - order.placedAt

        let firstDeadline = board.firstDeadline * 1000 / MS_PER_FRAME;
        let secondDeadline = board.secondDeadline * 1000 / MS_PER_FRAME;

        if(secondDeadline < framesElapsed && framesElapsed < secondDeadline + (500 / MS_PER_FRAME)){
            AssetManager.loadAliasedAudio(`buzzer.${board.id}`, 'buzzer.mp3').play(0.5);
        }

        ctx.beginPath();
        // @ts-ignore
        ctx.roundRect(index*sideLength, 0, sideLength, sideLength, 5);
        ctx.strokeStyle = 'rgba(0, 0, 0, 0.75)'
        ctx.lineWidth = 2
        ctx.stroke()

        if(framesElapsed > board.firstDeadline * 1000 / MS_PER_FRAME){
            ctx.beginPath()
            ctx.fillStyle =  framesElapsed > board.secondDeadline * 1000 / MS_PER_FRAME
                ? 'rgba(255, 0, 0, 0.5)'
                : 'rgba(255, 255, 0, 0.5)'
            // @ts-ignore
            ctx.roundRect(index*sideLength, 0,sideLength,sideLength, 5);
            ctx.fill();
        }

        const area = {
            x: index * sideLength,
            y: 0,
            width: sideLength,
            height: sideLength
        }

        renderFoodItemsInArea(area, order.foodItems, ctx)

        index++;
    }
}) as Renderer;

export function orderBoardReducer(object: GameObject, state: GameState) {
    if (!isOrderBoard(object)) return state;

    let board = object as OrderBoard;

    if (board.timestampForNextOrder < state.frameNumber) {
        let [[ _, getOrder ], ...upcomingOrders] = board.upcomingOrders;
        let duration = upcomingOrders[0]?.[0]
        if(duration === undefined) duration = Infinity
        board = {
            ...board,
            upcomingOrders,
            timestampForNextOrder: state.frameNumber + (duration*1000/MS_PER_FRAME),
            orders: [
                ...board.orders,
                {
                    foodItems: getOrder(),
                    placedAt: state.frameNumber,
                    fullfilledAt: null
                }
            ]
        }
    }

    return {
        ...state,
        objects: updateGameObjectById(state.objects, board)
    }
}

export function randomElement<T>(array: T[]) {
    return array[Math.floor(Math.random() * array.length)];
}

function generateRandomNumber(min: number, max: number) {
    return Math.floor(Math.random() * (max - min + 1) + min);
}