import { Dimension } from "Dimensions";
import { combineRenderers, create3DRenderer, createContentRenderer } from "Renderer";
import { GameObject, removeGameObjectById, updateGameObjectById } from "State";
import { CanBeCarried, CanHandleDrop, createCanBeCarried, createWithHitbox, createWithId, HasRenderer, hasHitbox, HasHitBox, HasId } from "Traits"
import { FoodItem, FoodItemId, isFoodItem, recipes } from "./FoodItem";

type FoodItemSet = Partial<Record<FoodItemId, number>>

export type Tray = HasId & HasHitBox & HasRenderer & CanBeCarried & CanHandleDrop & {
    isTray: true
    contents: FoodItemSet
}

export const createTray = (x?: Dimension, z?: Dimension): Tray => ({
    ...createWithId(),
    ...createCanBeCarried(),
    ...createWithHitbox('2studs', '2studs', x, z),
    isTray: true,
    contents: {},
    render: combineRenderers(
        create3DRenderer('tray'),
        createContentRenderer({ sprite: 'tray', content: (tray: Tray) => tray.contents })
    ),
    canHandleDrop,
    handleDropped
})

export const isTray = (x: any): x is Tray => x && x.isTray;

function canHandleDrop(object: GameObject): object is GameObject & HasHitBox {
    return hasHitbox(object) && isFoodItem(object)
}

function handleDropped(this: Tray, droppedFoodItem: FoodItem, objects: GameObject[]): GameObject[] {
    const formerQuantity = this.contents[droppedFoodItem.foodItemId] || 0;

    const tray: Tray = {
        ...this,
        contents: consolidateFoodItems({
            ...this.contents,
            [droppedFoodItem.foodItemId]: formerQuantity + 1
        })
    }

    objects = updateGameObjectById(objects, tray)

    objects = removeGameObjectById(objects, droppedFoodItem);

    return objects;
}

function consolidateFoodItems(foodItems: FoodItemSet): FoodItemSet {
    const results: FoodItemSet = {};
    const remainingIngredients: FoodItemSet = expand(foodItems);

    recipes.forEach( recipe => {
        const haveEverything = recipe.ingredientList.every( ingredient => Boolean(remainingIngredients[ingredient]) )
        if(haveEverything){
            results[recipe.name] = 1;
            for(let ingredient of recipe.ingredientList){
                const remaining = remainingIngredients[ingredient] || 0
                remainingIngredients[ingredient] = remaining - 1;
            }
        }
    })

    return { ...results, ...remainingIngredients };
}


function expand(foodItems: FoodItemSet){
    const expanded: FoodItemSet = {};

    for(let key in foodItems){
        let itemName = key as FoodItemId;

        const quantity = foodItems[itemName] || 0;

        const recipe = recipes.find( recipe => recipe.name === itemName)
        if (typeof recipe !== 'undefined'){
            for(let ingredientName of recipe.ingredientList){
                expanded[ingredientName] = (expanded[ingredientName] || 0) + quantity
            }
        } else {
            expanded[itemName] = (expanded[itemName] || 0) + quantity
        }
    }

    return expanded
}