import { Controls } from "Controls";
import { Character, isFinishedFryer, isTray, Tray } from "Objects";
import { GameObject, GameState, updateGameObjectById } from "State";
import { canProvide, filter } from "Traits";
import { CanBeCarried, canBeCarried, canBePickedUp } from "./CanBeCarried";
import { CanBeControlled, canBeControlled } from "./CanBeControlled";
import { getObjectsInfrontOf } from "./CanFaceADirection";
import { hasHitbox, HasHitBox } from "./HasHitbox";

export type CanCarry = {
    carrying: null | number;
    canCarry: true;
}

export const canCarry = (x: any): x is CanCarry => Boolean(x?.canCarry)

export const createCanCarry = (): CanCarry => ({
    carrying: null,
    canCarry: true
})

export function canCarryReducer(object: GameObject, state: GameState): GameState {
    if(!canCarry(object) || !canBeControlled(object)) return state;

    let carrier = object as Character;

    const controller = Controls[carrier.controllerId];

    let objects = state.objects

    if (controller.justReleased('primaryAction')) {

        const whatsBeingCarried = carrier.carrying
            ? objects.find( x => x.id === carrier.carrying) as GameObject & CanBeCarried & HasHitBox
            : null

        if(whatsBeingCarried?.canBeDropped(objects)){
            objects = whatsBeingCarried.handleDrop(objects, state.frameNumber);

            carrier = {
                ...carrier,
                carrying: null
            }

            objects = updateGameObjectById(objects, carrier)

            return { ...state, objects}
        }

        if(whatsBeingCarried && !isTray(whatsBeingCarried)){
            return state;
        }

        const tray = whatsBeingCarried as null | Tray;

        let targets = objects.filter( object => object.id !== tray?.id);

        let [ objectToPickUp ] = getObjectsInfrontOf(object, filter(targets, canBePickedUp, hasHitbox));
        const [ provider ] = getObjectsInfrontOf(object, filter(targets, canProvide))
        const [ finishedFryer ] = getObjectsInfrontOf(object, filter(targets, isFinishedFryer))

        if(!objectToPickUp && provider) {
            let provided = provider.provide();
            if(isTray(provided) && tray){

            } else {
                objectToPickUp = provided;
                objects = [ ...objects, objectToPickUp ]
            }
        }

        if(!objectToPickUp && finishedFryer){
            ([objects, objectToPickUp] = finishedFryer.retrieve(objects));
        }

        if (objectToPickUp && !tray) {
            objects = objectToPickUp.handlePickedUpBy(carrier, objects);
            carrier = {
                ...carrier,
                carrying: objectToPickUp.id
            }
        }

        if(objectToPickUp && tray && tray.canHandleDrop(objectToPickUp)){
            objects = objectToPickUp.handlePickedUpBy(carrier, objects)
            objects = tray.handleDropped(objectToPickUp, objects, state.frameNumber)
        }

    }

    return {
        ...state,
        objects: updateGameObjectById(objects, carrier)
    }
}
