import { canBeCarriedReducer, canBeControlledReducer, canBreadReducer, canCarryReducer, canChopReducer } from "Traits";
import { GameState } from "./GameState";
import { GameObject } from "./GameObject";
import { fryerReducer, orderBoardReducer, pickUpCounterReducer, timerReducer } from "Objects";
import { buttonReducer, loadLevelMenu, pauseMenuReducer } from "Menus";
import { Controls } from "Controls";
import { characterMenuReducer } from "Menus/CharacterMenu";
import { Persistence } from "./persistence";
import { AssetManager } from "Renderer";
import { tutorialScreenReducer } from "Menus/loadTutorialScreen";

const allReducers = [
    canBeControlledReducer,
    canCarryReducer,
    canChopReducer,
    canBeCarriedReducer,
    fryerReducer,
    canBreadReducer,
    orderBoardReducer,
    timerReducer,

    buttonReducer,

    startButtonReducer,
    pauseMenuReducer,
    tutorialScreenReducer,
    characterMenuReducer,
    pickUpCounterReducer
];

export function gameReducer(state: GameState){
    AssetManager.clearAudio();

    state = state.objects.reduce(objectReducer, {
        ...state,
        frameNumber: state.isPaused ? state.frameNumber : state.frameNumber + 1
    });

    Persistence.save('highScores', state.highScores);

    return state;
}

export function objectReducer(state: GameState, { id }: GameObject): GameState{
    return allReducers.reduce( (state, reducer) => {
        const object = state.objects.find( object => object.id === id);
        if(object) return reducer(object, state)
        return state
    }, state);
}


function startButtonReducer(object: GameObject, state: GameState){
    if(state.canPause) return state

    for(let controller of Controls){
        if(controller.justPressed('start') && state.selectedLevel === null){
            return {
                ...state,
                isStarted: true,
                objects: loadLevelMenu(state)
            }
        }

        const atLeastOneCharacterSelected = Object.values(state.selectedCharacters).some( selection => selection !== null)

        if(controller.justPressed('start') && state.selectedLevel !== null && atLeastOneCharacterSelected){
            return {
                ...state,
                canPause: true,
                objects: state.selectedLevel.getObjects(state)
            }
        }
    }

    return state
}