import { coalesceX, Direction, Pixels, Vector } from "Dimensions";
import { GameObject, GameState, updateGameObjectById, updateGameObjectsWhere } from "State";
import { CanCarry } from "./CanCarry";
import { CanFaceADirection, getObjectsInfrontOf } from "./CanFaceADirection";
import { CanHandleDrop, canHandleDrop } from "./CanHandleDrop";
import { counterSpaceContains } from "./HasCounterSpace";
import { HasHitBox } from "./HasHitbox";

export type CanBeCarried = {
    canBeCarried: true;
    carriedBy: number | null;
    handlePickedUpBy: typeof handlePickedUpBy;
    canBeDropped: typeof canBeDropped;
    handleDrop: typeof handleDrop;
}

export const canBeCarried = (x: any): x is CanBeCarried => Boolean(x?.canBeCarried)

export const canBePickedUp = (x: any): x is CanBeCarried => canBeCarried(x) && x.carriedBy === null

export const createCanBeCarried = (): CanBeCarried => ({
    canBeCarried: true,
    handlePickedUpBy,
    carriedBy: null,
    canBeDropped,
    handleDrop
})

function handlePickedUpBy(this: GameObject & CanBeCarried & HasHitBox, carrier: GameObject & HasHitBox & CanFaceADirection & CanCarry, objects: GameObject[]) {
    let object: GameObject & CanBeCarried & HasHitBox = {
        ...this,
        carriedBy: carrier.id
    }

    object = positionInfrontOfCarrier(object, objects)

    objects = updateGameObjectById(objects, object)

    objects = updateGameObjectsWhere(objects, counterSpaceContains(object), space => ({
        ...space,
        counterSpaceTakenBy: null
    }))

    return objects
}

function canBeDropped(this: GameObject & CanBeCarried & HasHitBox, objects: GameObject[]): boolean {
    const carrier = objects.find( carrier => carrier.id === this.carriedBy) as HasHitBox & CanCarry & CanFaceADirection

    const [ somethingToDropOn ] = getObjectsInfrontOf(carrier, objects.filter(canHandleDrop(this))) as CanHandleDrop[];

    if(somethingToDropOn){
        return true;
    }

    return false;
}

function handleDrop(this: GameObject & CanBeCarried & HasHitBox, objects: GameObject[], frameNumber: number): GameObject[] {

    let object = {
        ...this,
        carriedBy: null
    }

    const carrier = objects.find( carrier => carrier.id === this.carriedBy) as HasHitBox & CanCarry & CanFaceADirection

    let [ somethingToDropOn ] = getObjectsInfrontOf(carrier, objects.filter(canHandleDrop(this))) as (GameObject & CanHandleDrop & HasHitBox)[];

    objects = updateGameObjectById(objects, object);

    objects = somethingToDropOn.handleDropped(object, objects, frameNumber);

    return objects
}

function positionInfrontOfCarrier(object: GameObject & HasHitBox & CanBeCarried, objects: GameObject[]){
    const carrier = objects.find( carrier => carrier.id === object.carriedBy) as HasHitBox & CanCarry & CanFaceADirection
    const offset = offsets[carrier.facing]
    return {
        ...object,
        hitbox: {
            ...object.hitbox,
            x: carrier.hitbox.x + offset.x as Pixels,
            z: carrier.hitbox.z + offset.z as Pixels,
            y: coalesceX('2studs')
        }
    }
}

export function canBeCarriedReducer(object: GameObject, state: GameState): GameState {
    if(!canBeCarried(object) || object.carriedBy === null) return state;

    object = positionInfrontOfCarrier(object, state.objects);

    return {
        ...state,
        objects: updateGameObjectById(state.objects, object)
    }
}


const offsets : Record<Direction, Vector> = {
    north: {
        x: 0,
        z: +coalesceX('1.5studs')
    },
    south: {
        x: 0,
        z: -coalesceX('1.5studs')
    },
    east: {
        x: +coalesceX('1.5studs'),
        z: 0
    },
    west: {
        x: -coalesceX('1.5studs'),
        z: 0
    }
}