import { coalesceX, Dimension, HEIGHT, Pixels } from "Dimensions";
import { Animations, Animation } from "Renderer";
import { AssetManager } from "Renderer";
import { GameObject, GameState } from "State";
import { Renderer, HasHitBox, Hitbox } from "Traits";


export const create3DRenderer = <T extends GameObject & HasHitBox>(options: Create3DRendererOptions<T>): Renderer => {
    const renderer = (state: GameState, object: T, ctx: CanvasRenderingContext2D) => {
        const { spriteKey, padding, speed } = process3DRendererOptions(options, object, state);

        if(spriteKey == null) return;

        const animation = Animations[spriteKey];

        const numberOfSyntheticFrames = Math.ceil(animation.numberOfFrames / speed)

        const ratio = animation.numberOfFrames / numberOfSyntheticFrames

        const currentSyntheticAnimationFrame = state.frameNumber % numberOfSyntheticFrames+1;

        const currentAnimationFrame = Math.max(1, Math.min(Math.ceil(currentSyntheticAnimationFrame * ratio), animation.numberOfFrames))

        if (animation.sideview) {
            ctx.drawImage(
                AssetManager.getImage(`${spriteKey}/${currentAnimationFrame}.sideview.png`),
                ...getSpriteCoords({ ...object.hitbox, y: 0 as Pixels }, animation.sideview.size)
            )
        }

        ctx.drawImage(
            AssetManager.getImage(spriteKey + `/${currentAnimationFrame}.png`),
            ...getSpriteCoords(object.hitbox, animation.size, padding)
        );
    }

    return renderer as Renderer;
}


type SpriteKey = keyof typeof Animations | null;

type SpriteKeyOption<T> = SpriteKey | ((object: T, state: GameState) => SpriteKey);
type SpeedOption<T> = number | ((object: T, state: GameState) => number);

export type Create3DRendererOptions<T> = SpriteKeyOption<T> | { sprite: SpriteKeyOption<T>, speed?: SpeedOption<T>, padding?: Dimension };

export function process3DRendererOptions<T>(options: Create3DRendererOptions<T>, object: T, state: GameState) {
    let spriteKey, padding, speed;

    if(options === null){
        padding = 0;
        speed = 1;
        spriteKey = null;
    } else if (typeof options === 'function') {
        padding = 0;
        speed = 1;
        spriteKey = options(object, state);
    } else if (typeof options !== 'object') {
        padding = 0;
        speed = 1;
        spriteKey = options
    } else {
        spriteKey = typeof options.sprite === 'function' ? options.sprite(object, state) : options.sprite
        speed = typeof options.speed === 'function' ? options.speed(object, state) : (options.speed || 1)
        padding = coalesceX(options.padding || 0)
    }

    speed = Math.min(1, speed)

    return { spriteKey, padding, speed }
}


export function getSpriteCoords(hitbox: Hitbox, size: Animation['size'], padding: number = 0): [number, number, number, number] {
    const scale = (hitbox.dx + padding) / size.width;

    const width = hitbox.dx + padding;

    const height = size.height * scale;

    const x = hitbox.x - (padding / 2);

    const y = HEIGHT - (hitbox.z + hitbox.y + (height) + padding / 3);

    return [x, y, width, height]
}
