import { GameState, initialState, gameReducer } from 'State';
import { createGameRenderer } from 'Renderer';
import { Controls, recordInputs } from 'Controls';
import { Persistence } from 'State/persistence';

export const MS_PER_FRAME = 1000 / 25;

export function startGame(){
    let state: GameState = initialState

    const canvas = document.createElement('canvas');

    const renderFrame = createGameRenderer(canvas);

    const history: GameState[] = [];

    function runFrame(){
        // Save Current State
        // history.push(state);

        // Run Reducers
        state = gameReducer(state);

        // Save that the current input states have been addressed
        recordInputs();

        // Render
        renderFrame(state)
    }

    const interval = setInterval(runFrame, MS_PER_FRAME);

    function clearData(){
        clearInterval(interval);
        localStorage.clear();
        window.location.reload();
    }

    function startDevMode(){
        Persistence.save('devMode', true);
        window.location.reload();
    }

    function stopDevMode(){
        Persistence.save('devMode', false);
        window.location.reload();
    }

    const game = {
        history,
        canvas,
        clearData,
        startDevMode,
        stopDevMode
    }

    return game;
}
