import { Level } from "Levels";
import { Character, createFloor, createOrderBoard, createScoreCounter, createTimer, FoodItemId, OrderTimeline, randomElement } from "Objects";
import { GameState } from "State";
import { appl, blbr, bTb1, bTb2, buns, cBrd, cfaS, chic, fry1, fry2, fry3, ltce, orng, pikl, piUp, ptat, strb, Tile, tileLevel, trsh, trSt, wall } from "./utils";

const map = (cha1: Tile, cha2: Tile, cha3: Tile, cha4: Tile) => ([
    [wall, wall, wall, wall, wall, wall, wall, wall, piUp, piUp, wall, wall, wall, wall, wall, wall, wall, wall, wall, wall],
    [null, null, fry3, fry3, null, null, wall, null, null, null, null, wall, null, null, fry1, fry1, fry2, fry2, null, null ],
    [null, cBrd, null, null, null, null, wall, null, cha1, cha2, null, wall, null, null, null, null, null, null, null, null ],
    [null, ptat, null, null, null, null, wall, null, cha3, cha4, null, wall, null, null, null, null, null, null, chic, null ],
    [null, null, null, null, null, null, trSt, null, null, null, null, trSt, null, null, null, null, null, null, chic, null ],
    [null, null, null, null, null, null, null, null, null, null, null, null, null, null, null, null, null, null, trsh, null ],
    [null, null, pikl, buns, null, null, null, null, null, null, null, null, null, null, bTb1, bTb1, bTb2, bTb2, null, null ],
])

export const TheOriginal : Level = {
    id: 1,
    name: 'The Original',
    targetScores: [ 5, 10, 15 ],
    tutorialEntries: [ 'original-sandwich', 'original-sandwich-no-pickles', 'waffle-fries' ],
    getObjects(state: GameState){
        return [
            createFloor(),
            createOrderBoard(orders, state),
            createTimer(state.frameNumber, 180 * 1000 / (1000/25)),
            createScoreCounter(),
            ...tileLevel(map, state)
        ]
    }
}

function getEntree(): FoodItemId {
    return randomElement([ 'original-sandwich', 'original-sandwich-no-pickles' ])
}

function getMeal(): FoodItemId[] {
    return [ getEntree(), 'waffle-fries' ]
}

function getEntreeOnly(): FoodItemId[]{
    return [ getEntree() ]
}

const orders: OrderTimeline = [
    [0, () => [ 'original-sandwich' ]],
    [5, () => [ 'original-sandwich-no-pickles']],
    [20, () => [ 'waffle-fries' ] ],
    [20, getMeal],
    [15, getEntreeOnly],
    [15, getMeal],
    [25, getMeal],
    [0, getMeal],
    [15, getEntreeOnly],
    [0, getMeal],
    [5, getEntreeOnly],
    [5, getMeal],
    [15, getMeal],
    [0, getMeal],
    [5, getEntreeOnly],
    [5, getMeal],
    [5, getEntreeOnly]
]