import { coalesceX, Dimension } from "Dimensions";
import { FoodItemId, Tray } from "Objects";
import { Animations, getSpriteCoords } from "Renderer";
import { AssetManager } from "Renderer";
import { GameObject, GameState } from "State";
import { HasHitBox, Renderer } from "Traits";
import { renderFoodItemsInArea } from "./renderFoodItemsInArea";


export const createContentRenderer = <T extends GameObject & HasHitBox>(options: CreateContentRendererOptions<T>): Renderer => {
    const renderer = (state: GameState, object: T, ctx: CanvasRenderingContext2D) => {
        const { spriteKey, content } = processContentRendererOptions(options, object)

        const objectAnimation = Animations[spriteKey];

        const [ x, y, width, height ] = getSpriteCoords(object.hitbox, objectAnimation.size);

        const foodItems = Object.keys(content) as FoodItemId[]
    
        const presentFoodItems = foodItems.filter( (foodItem) => content[foodItem] !== 0);

        renderFoodItemsInArea({ x, y, width, height }, presentFoodItems, ctx)
    }

    return renderer as Renderer;
}

type SpriteKey = keyof typeof Animations;
type Content = Partial<Record<FoodItemId, number>>;

type SpriteKeyOption<T> = SpriteKey | ((object: T) => keyof typeof Animations);
type ContentOption<T> = FoodItemId | null | Content | ((object: T) => FoodItemId | null | Content);


export type CreateContentRendererOptions<T> = { sprite: SpriteKeyOption<T>, content: ContentOption<T> };

export function processContentRendererOptions<T>(options: CreateContentRendererOptions<T>, object: T) {
    let spriteKey = typeof options.sprite === 'function' ? options.sprite(object) : options.sprite
    let content: Content;

    let contentOption;
    if(typeof options.content === 'function'){
       contentOption = options.content(object)
    } else {
        contentOption = options.content
    }

    if(contentOption === null){
        content = {}
    } else if(typeof contentOption === 'object'){
        content = contentOption
    } else {
        content = {
            [contentOption]: 1
        }
    }

    return { spriteKey, content }
}