import { coalesceX, Dimension, HEIGHT, Pixels } from "Dimensions";
import { Animations, Animation } from "Renderer";
import { AssetManager } from "Renderer";
import { MS_PER_FRAME } from "startGame";
import { GameObject, GameState } from "State";
import { Renderer, HasHitBox, Hitbox, HasBoundingbox } from "Traits";

export const create2DRenderer = <T extends GameObject & HasBoundingbox>(options: Create2DRendererOptions<T>): Renderer => {
    const renderer = function(this: HasBoundingbox, state: GameState, object: T, ctx: CanvasRenderingContext2D){
        const { spriteKey, speed, fade } = process2DRendererOptions(options, object, state);

        let { width, height, x, y } = object.boundingbox;

        if(spriteKey == null) return;

        const animation = Animations[spriteKey];

        const numberOfSyntheticFrames = Math.ceil(animation.numberOfFrames / speed)

        const ratio = animation.numberOfFrames / numberOfSyntheticFrames

        const currentSyntheticAnimationFrame = state.frameNumber % numberOfSyntheticFrames+1;

        const currentAnimationFrame = Math.max(1, Math.min(Math.ceil(currentSyntheticAnimationFrame * ratio), animation.numberOfFrames))

        width = width || animation.size.width as Pixels;

        const scale = width / animation.size.width;

        height = animation.size.height * scale as Pixels

        if(fade){
            const { startAt, duration } = fade;
            const elapsed = state.frameNumber - startAt;
            const finalFrame = duration / MS_PER_FRAME ;
            ctx.globalAlpha = elapsed / finalFrame
        }

        ctx.drawImage(
            AssetManager.getImage(spriteKey + `/${currentAnimationFrame}.png`),
            x,
            y,
            width,
            height
        );
            
        ctx.globalAlpha = 1;
    }

    return renderer as Renderer;
}

type SpriteKey = keyof typeof Animations | null;
type SpriteKeyOption<T> = SpriteKey | ((object: T, state: GameState) => SpriteKey);
type SpeedOption<T> = number | ((object: T, state: GameState) => number);

export type Create2DRendererOptions<T> = SpriteKeyOption<T> | { 
    sprite: SpriteKeyOption<T>, 
    fade?: { startAt: number, duration: number }
    speed?: SpeedOption<T> 
};

export function process2DRendererOptions<T>(options: Create2DRendererOptions<T>, object: T, state: GameState) {
    let spriteKey, speed, fade;

    if(options === null){
        speed = 1;
        spriteKey = null;
    } else if (typeof options === 'function') {
        speed = 1;
        spriteKey = options(object, state);
    } else if (typeof options !== 'object') {
        speed = 1;
        spriteKey = options
    } else {
        spriteKey = typeof options.sprite === 'function' ? options.sprite(object, state) : options.sprite
        speed = typeof options.speed === 'function' ? options.speed(object, state) : (options.speed || 1)
        fade = options.fade
    }

    speed = Math.min(1, speed)

    return { spriteKey, speed, fade }
}

