import { Controls } from "Controls";
import { Dimension } from "Dimensions";
import { Levels } from "Levels";
import { GameObject, GameState } from "State";
import { HasBoundingbox } from "Traits";
import { createButton } from "./Button";
import { loadFinishedLevelMenu } from "./loadFinishedLevelMenu";
import { createOverlay } from "./Overlay";


export const loadPausedLevelMenu = () => {

    const objects = [
        createOverlay(),
        createButton({ text: 'Continue', color: 'white' }, true, '50%', '40%', handleContinue),
        createButton({ text: 'Quit', color: 'white' }, false, '50%', '50%', handleQuit)
    ]

    function handleContinue(state: GameState): GameState {
        return {
            ...state,
            isPaused: false,
            objects: state.objects.filter( object => !menuObjectIds.includes(object.id))
        }
    }

    function handleQuit(state: GameState): GameState {
        return {
            ...state,
            objects: [
                ...state.objects.filter( object => !menuObjectIds.includes(object.id)),
                ...loadFinishedLevelMenu(state)
            ]
        }
    }

    const menuObjectIds = objects.map( object => object.id )

    return objects;
}

export function pauseMenuReducer(_: GameObject, state: GameState){
    if(!state.canPause) return state;

    for(let controller of Controls){
        if(controller.justPressed('minus') && !state.isPaused){
            return {
                ...state,
                isPaused: true,
                objects: [
                    ...state.objects,
                    ...loadPausedLevelMenu()
                ]
            }
        }
    }

    return state
}