import { coalesceX, coalesceY, Dimension } from "Dimensions";
import {  create2DRenderer } from "Renderer";
import {  createWithBoundingbox, createWithId, HasBoundingbox, HasId, HasRenderer } from "Traits";

type LegoLogo = HasId & HasBoundingbox & HasRenderer;

export const createLegoLogo = (width: Dimension = 0, height: Dimension = 0, x: Dimension = 0, y: Dimension = 0): LegoLogo => ({
    ...createWithId(),
    ...createWithBoundingbox(width, height, x, y),
   render: create2DRenderer({ sprite: 'lego-logo', fade: { 
    startAt: 1, duration: 2000
   } })
})