import { Dimension } from "Dimensions";
import { AssetManager, combineRenderers, create2DRenderer, create3DRenderer } from "Renderer";
import { MS_PER_FRAME } from "startGame";
import { GameObject, GameState, removeGameObjectById, updateGameObjectById } from "State";
import { CanBlockMovement, createCanBlockMovement, createWithHitbox, createWithId, HasRenderer, HasCounterSpace, HasHitBox, HasId, CanHandleDrop, hasHitbox, filter } from "Traits";
import { FoodItemId } from "./FoodItem";
import { isOrderBoard, Order, randomElement } from "./OrderBoard";
import { isTray, Tray } from "./Tray";

const guests = [
    'violet',
    'ursula',
    'storm-trooper',
    'santa',
    'leia',
    'iron-man',
    'elrond',
    'captain-america',
    'batman',
    'aang'
]

export type PickUpCounter = HasId & HasHitBox & CanBlockMovement & HasRenderer & CanHandleDrop & {
    isPickupCounter: true;
    lastFullfillment: number | null;
    currentGuest: string;
};

export const createPickUpCounter = (x?: Dimension, z?: Dimension): PickUpCounter => ({
    isPickupCounter: true,
    ...createWithId(),
    ...createWithHitbox('4studs', '2studs', x, z, '1studs'),
    ...createCanBlockMovement(),
    currentGuest: randomElement(guests),
    render: combineRenderers(
        create3DRenderer<PickUpCounter>({ sprite: c => `guests/${c.currentGuest}`, padding: -30 }),
        create3DRenderer('pick-up-counter')
    ),
    canHandleDrop,
    handleDropped,
    lastFullfillment: null
})

export const isPickupCounter = (x: any): x is PickUpCounter => x?.isPickupCounter

const canHandleDrop = (x: any): x is GameObject & HasHitBox => {
    return hasHitbox(x) && isTray(x)
}

function handleDropped(this: PickUpCounter, tray: Tray, objects: GameObject[], frameNumber: number){

    let [ orderBoard ] = filter(objects, isOrderBoard);

    if(orderBoard) {

        let orders: Order[] = [ ...orderBoard.orders ]
        let fullfilled: Order[] = [ ...orderBoard.fullfilled ]
        for(let i = 0; i < orders.length; i++){
            const order = orders[i]
            const foodItems = order.foodItems;
            if(foodItems.every( foodItem => foodItem in tray.contents)){
                orders.splice(i, 1)
                fullfilled = [ ...fullfilled, {
                    ...order,
                    fullfilledAt: frameNumber
                }]
                break;
            }
        }

        let pickupCounter = {
            ...this,
            lastFullfillment: fullfilled.length !== orderBoard.fullfilled.length ? frameNumber : this.lastFullfillment,
            currentGuest: fullfilled.length !== orderBoard.fullfilled.length ? randomElement(guests) : this.currentGuest,
        }

        orderBoard = {
            ...orderBoard,
            orders,
            // @ts-ignore
            fullfilled
        }

        objects = updateGameObjectById(objects, pickupCounter)

        objects = updateGameObjectById(objects, orderBoard)

    }

    objects = removeGameObjectById(objects, tray)

    return objects;
}

export function pickUpCounterReducer(object: GameObject, state: GameState){
    if(!isPickupCounter(object)) return state;

    if(object.lastFullfillment !== null && state.frameNumber - (1000 / MS_PER_FRAME) < object.lastFullfillment){
        AssetManager.loadAliasedAudio('success', 'success.mp3').play();
    }

    return state;
}

