import { AssetManager } from "./AssetManager"
import animationData from 'animations.json'

export type Animation = {
    numberOfFrames: number,
    sideview: false | Pick<Animation, 'size'>,
    size: {
        width:  number,
        height: number
    },
    handleOffset: "bottom-left" | "bottom-center"
}

export const Animations = animationData as Record<string, Animation>

export const assetLoading = Promise.all(Object.keys(Animations).map( assetPath => {
    const animation = Animations[assetPath];
    return Promise.all(new Array(animation.numberOfFrames).fill(1).map( (_, frameIndex) => {
        return animation.sideview
            ? Promise.all([
                AssetManager.loadImage(`${assetPath}/${frameIndex+1}.png`),
                AssetManager.loadImage(`${assetPath}/${frameIndex+1}.sideview.png`)
            ])
            : AssetManager.loadImage(`${assetPath}/${frameIndex+1}.png`)
    }))
}))