import { coalesceX, Dimension, HEIGHT, Pixels } from "Dimensions";
import { Animations, Animation } from "Renderer";
import { AssetManager } from "Renderer";
import { MS_PER_FRAME } from "startGame";
import { GameObject, GameState } from "State";
import { Renderer, HasHitBox, Hitbox, HasBoundingbox, HasOrigin } from "Traits";

export const createTextRenderer = <T extends GameObject & HasOrigin>(options: CreateTextRendererOptions<T>): Renderer => {
    const renderer = function(this: HasBoundingbox, state: GameState, object: T, ctx: CanvasRenderingContext2D){
        let { x, y } = object.origin;
        renderText(ctx, state, x, y, processTextRendererOptions(options, object, state))
    }

    return renderer as Renderer;
}

export function renderText(ctx: CanvasRenderingContext2D, state: GameState, x: number, y: number, options: ReturnType<typeof processTextRendererOptions>){
    const { text, font, fade, align, baseline, color } = options;

    if(text === null) return;

    if(fade){
        const { startAt, duration, loop } = fade;
        const elapsed = state.frameNumber - startAt;
        const finalFrame = duration / MS_PER_FRAME ;
        if(elapsed < 0){
            ctx.globalAlpha = 0
        }else if(!loop || elapsed < finalFrame){
            ctx.globalAlpha = elapsed / finalFrame
        } else {
            let currentIteration = Math.floor(elapsed / finalFrame)
            let elapsedThisIteration = (elapsed % finalFrame) / finalFrame;
            if(currentIteration % 2 == 0){ // Fade In
                ctx.globalAlpha = elapsedThisIteration
            } else { // Fade Out
                ctx.globalAlpha = 1-elapsedThisIteration
            }
        }
    }

    ctx.save();
    if(font){
        ctx.font = font
    }

    ctx.fillStyle = color
    ctx.textAlign = align;
    ctx.textBaseline = baseline;

    ctx.fillText(
        text,
        x,
        y,
    );

    ctx.restore()
    ctx.globalAlpha = 1;
}

type TextOption<T> = string | ((object: T, state: GameState) => string);

export type CreateTextRendererOptions<T> = TextOption<T> | {
    text: TextOption<T>,
    font?: string,
    color?: string,
    baseline?: CanvasTextBaseline,
    align?: CanvasTextAlign,
    fade?: { startAt: number, duration: number, loop?: boolean }
};

export function processTextRendererOptions<T>(options: CreateTextRendererOptions<T>, object: T, state: GameState) {
    let text, font, fade;
    let color = 'black'
    let align: CanvasTextAlign = 'center';
    let baseline: CanvasTextBaseline = 'middle';

    if(options === null){
        text = null;
    } else if (typeof options === 'function') {
        text = options(object, state);
    } else if (typeof options !== 'object') {
        text = options
    } else {
        text = typeof options.text === 'function' ? options.text(object, state) : options.text
        align = options.align || align
        baseline = options.baseline || baseline
        color = options.color || color
        font = options.font
        fade = options.fade
    }

    return { text, fade, font, color, align, baseline }
}

