import { coalesceX, Dimension } from "Dimensions";
import { create3DRenderer } from "Renderer";
import { GameObject } from "State";
import { CanBlockMovement, createCanBlockMovement, createWithCounterSpace, createWithHitbox, createWithId, HasRenderer, HasCounterSpace, HasHitBox, HasId } from "Traits";

export type Wall = HasId & HasHitBox & CanBlockMovement & HasCounterSpace & HasRenderer;

export const createWall = (x?: Dimension, z?: Dimension): Wall => ({
    ...createWithId(),
    ...createWithHitbox('2studs', '2studs', x, z, '1studs'),
    ...createCanBlockMovement(),
    ...createWithCounterSpace(),
    render: create3DRenderer('walls/normal-wall'),
})

export const tileWallX = (x: Dimension, z: Dimension, objects: (GameObject | null)[]) => {
    let results: GameObject[] = []
    objects.forEach( (object, i) => {
        let wall = createWall(coalesceX(x) + coalesceX('2studs')*i, z)

        results.push(wall)

        if(object !== null && wall.canHandleDrop(object)){
            results.push(object)
            
            results = wall.handleDropped(object, results, 1);
        } 
    })
    return results;
}


export const tileWallZ = (x: Dimension, z: Dimension, objects: (GameObject | null)[]) => {
    let results: GameObject[] = []
    objects.forEach( (object, i) => {
        let wall = createWall(x, coalesceX(z) + coalesceX('2studs')*i)

        results.push(wall)

        if(object !== null && wall.canHandleDrop(object)){
            results.push(object)
            
            results = wall.handleDropped(object, results, 1);
        } 
    })
    return results;
}