import { Controls } from "Controls";
import { coalesceX, coalesceY, Dimension } from "Dimensions";
import { Level, Levels } from "Levels";
import { calculateFinalScore, createScoreCounter, createStarDisplay } from "Objects";
import { GameObject, GameState } from "State";
import { HasBoundingbox } from "Traits";
import { createButton } from "./Button";
import { loadLevelMenu } from "./loadLevelMenu";
import { createOverlay } from "./Overlay";


export const loadFinishedLevelMenu = (state: GameState) => {

    const finalScore = calculateFinalScore(state);

    let stars: 0 | 1 | 2 | 3 = 0;

    const level = state.selectedLevel as Level;

    if(finalScore >= level.targetScores[0]){
        stars = 1
    }

    if(finalScore >= level.targetScores[1]){
        stars = 2
    }

    if(finalScore >= level.targetScores[2]){
        stars = 3
    }

    const formerHighScore = state.highScores[level.id] || 0;
    const postLevelState : GameState = {
        ...state,
        isPaused: false,
        canPause: false,
        selectedLevel: null,
        highScores: {
            ...state.highScores,
            [level.id]: Math.max(formerHighScore, stars) as 0 | 1 | 2 | 3
        }
    }

    const objects = [
        createOverlay(),
        createScoreCounter('3.5studs', '50%', '25%'),
        createStarDisplay(stars, '20%', '50%', '45%', Date.now(), level.targetScores.map( score => score.toString())),
        createButton({ text: 'Return to Menu', color: 'white' }, true, '50%', '60%', handleReturnToLevelMenu)
    ]



    function handleReturnToLevelMenu(): GameState{

        return {
            ...postLevelState,
            objects: loadLevelMenu(postLevelState)
        }
    }

    return objects;
}
