import { Dimension } from "Dimensions";
import { combineRenderers, create3DRenderer, createContentRenderer, createProgressRenderer, createSpicyIconRenderer } from "Renderer";
import { MS_PER_FRAME } from "startGame";
import { GameObject, GameState, removeGameObjectById, updateGameObjectById } from "State";
import { createWithHitbox, createWithId, HasRenderer, HasHitBox, HasId, CanHandleDrop, hasHitbox, CanBlockMovement, createCanBlockMovement } from "Traits";
import { CanBeSpicy } from "Traits/CanBeSpicy";
import { Character } from "./Character";
import { createFoodItem, FoodItem, FoodItemId, isFoodItem } from "./FoodItem";

type BreadableId = 'raw-chicken-breast'|'raw-nuggets' ;

type SpicyBreadableId = 'raw-chicken-breast'

const breadableIDs = [ 'raw-chicken-breast','raw-nuggets' ]

const spicyBreadableIDs = [ 'raw-chicken-breast' ]


export type BreadingTable = HasId & HasHitBox & HasRenderer & CanHandleDrop & CanBlockMovement & CanBeSpicy & {
    isBreadingTable: true,
    lastInUse: null | number,
    content: null | BreadableId,
    progress: null | number,
    canBread: () => boolean,
    bread: (breader: Character, objects: GameObject[], frameNumber: number) => GameObject[]
};

export const isBreadingTable = (x: any): x is BreadingTable => x && x.isBreadingTable

export const createBreadingTable = (isSpicy = false, x?: Dimension, z?: Dimension): BreadingTable => ({
    ...createWithId(),
    isSpicy,
    isBreadingTable: true,
    lastInUse: null,
    content: null,
    progress: null,
    ...createWithHitbox('4studs', '2studs', x, z, '1studs'),
    ...createCanBlockMovement(),
    render: combineRenderers(
        create3DRenderer('breading-table'),
        createSpicyIconRenderer(),
        createContentRenderer({ sprite: 'breading-table', content: (breadingTable: BreadingTable) => breadingTable.content }),
        createProgressRenderer('breading-table'),
        create3DRenderer({ sprite: getFlour, speed: 0.25 }),
    ),
    canHandleDrop,
    handleDropped,
    canBread,
    bread
})

const getFlour = (breadingTable: BreadingTable, state: GameState) => {
    if(breadingTable.lastInUse != null && breadingTable.lastInUse > (state.frameNumber-10)){
        return 'flour'
    } else {
        return null
    }
}


export type Breadable = FoodItem & {
    foodItemId: BreadableId
}

const isBreadable = (x: any): x is Breadable => isFoodItem(x) && breadableIDs.includes(x.foodItemId)

const isSpicyBreadable = (x: any): x is Breadable => isFoodItem(x) && spicyBreadableIDs.includes(x.foodItemId)


function canHandleDrop(this: BreadingTable, object: GameObject): object is GameObject & HasHitBox {
    return hasHitbox(object)
        && ((!this.isSpicy && isBreadable(object)) || (this.isSpicy && isSpicyBreadable(object)))
        &&  this.content === null
}

function handleDropped(this: BreadingTable, droppedObject: Breadable, objects: GameObject[]){
    const breadingTable: BreadingTable = {
        ...this,
        content: droppedObject.foodItemId,
        progress: 0
    }

    objects = removeGameObjectById(objects, droppedObject)

    objects = updateGameObjectById(objects, breadingTable)

    return objects;
}

function canBread(this: BreadingTable){
    return this.content !== null
}

function bread(this: BreadingTable, chopper: Character, objects: GameObject[], frameNumber: number){
    if(this.content === null) return objects

    let newProgress = (this.progress || 0) + 0.01;

    let breadingTable: BreadingTable;

    if(newProgress < 1){
        breadingTable = {
            ...this,
            lastInUse: frameNumber,
            progress: newProgress
        }
    } else {
        const newFoodItem = this.isSpicy
            ? createFoodItem(spicyBreadYield[this.content as SpicyBreadableId])
            : createFoodItem(breadYield[this.content])

        breadingTable = {
            ...this,
            progress: null,
            content: null
        }

        objects = [ ...objects, newFoodItem ]

        objects = newFoodItem.handlePickedUpBy(chopper, objects)

        objects = updateGameObjectById(objects, {
            ...chopper,
            carrying: newFoodItem.id
        })
    }

    objects = updateGameObjectById(objects, breadingTable)

    return objects
}

export const breadYield : Record<BreadableId, FoodItemId> = {
    'raw-chicken-breast': 'original-raw-filet',
    'raw-nuggets':'raw-breaded-nuggets'
}

export const spicyBreadYield : Record<SpicyBreadableId, FoodItemId> = {
    'raw-chicken-breast': 'spicy-raw-filet'
}