export const filter = <I, T1, T2, T3, T4>(
    objects: I[], 
    callback1: (object: any) => object is T1,
    callback2?: (object: any) => object is T2,
    callback3?: (object: any) => object is T3,
    callback4?: (object: any) => object is T4
): (I & T1 & T2 & T3 & T4)[] => {
    // @ts-ignore
    return objects.filter((object: any): object is T1 & T2 & T3 & T4 => {
        return callback1(object) 
            && (!callback2 || callback2(object))
            && (!callback3 || callback3(object))
            && (!callback4 || callback4(object))
    })
}