import { Controls } from "Controls";
import { Character, isCuttingBoard } from "Objects";
import { AssetManager } from "Renderer";
import { GameObject, GameState, updateGameObjectById } from "State";
import { filter } from "Traits";
import { canBeControlled } from "./CanBeControlled";
import { canCarry } from "./CanCarry";
import { getObjectsInfrontOf } from "./CanFaceADirection";


export type CanChop = {
    canChop: true;
    chopping: boolean;
}

export const canChop = (x: any): x is CanChop => Boolean(x?.canChop)

export const createCanChop = (): CanChop => ({
    canChop: true,
    chopping: false,
})

export function canChopReducer(object: GameObject, state: GameState): GameState {
    if(!canChop(object) || !canBeControlled(object) || !canCarry(object)) return state;

    const controller = Controls[object.controllerId];

    let chopper = object as Character;

    let objects = state.objects;

    chopper = {
        ...chopper,
        chopping: false
    }

    if (controller.isPressed('secondaryAction') && object.carrying === null && !state.isPaused){
        const [ cuttingBoard ] = getObjectsInfrontOf(object, filter(state.objects, isCuttingBoard));

        if(cuttingBoard && cuttingBoard.canChop()){
            chopper = {
                ...chopper,
                chopping: true
            }
            objects = updateGameObjectById(objects, chopper)
            objects = cuttingBoard.chop(chopper, objects);
            chopper = objects.find( object => object.id === chopper.id) as Character
        }
    }

    let track = AssetManager.loadAliasedAudio(`chopping.${chopper.id}`, 'chopping.mp3');
    if(chopper.chopping){
        track.play(0.15);
    }

    objects = updateGameObjectById(objects, chopper)

    return {
        ...state,
        objects
    }
}
