import { GameObject } from "State"
import { HasHitBox } from "./HasHitbox"

export type CanHandleDrop = {
    canHandleDrop(object: GameObject): object is GameObject & HasHitBox
    handleDropped(this: GameObject & CanHandleDrop & HasHitBox, object: GameObject & HasHitBox, objects: GameObject[], frameNumber: number): GameObject[]
}

export const canHandleDrop = (object: GameObject) => (x: any): x is CanHandleDrop => {
    if(typeof x.canHandleDrop === 'function'){
        return x.canHandleDrop(object);
    } 
    return false
}