import { ControllerId } from "Controls";
import { Dimension } from "Dimensions";
import { Levels } from "Levels";
import { create3DRenderer } from "Renderer";
import { GameState } from "State";
import { createWithHitbox, createWithId, HasHitBox, HasId, createCanBeControlled, CanBeControlled, HasRenderer, CanCarry, createCanCarry, CanChop, createCanChop, createCanBread, CanBread } from "Traits";

export type CharacterId =
    | 'truett'
    | 'jeannette'
    | 'hoho'
    | 'nonni'
    | 'james'
    | 'mandi'
    | 'charles'
    | 'julie'
    | 'tori'
    | 'josh'
    | 'joy'
    | 'miles'
    | 'isaac'
    | 'lydia';

export const CharacterIds: CharacterId[] = [
    'truett', 'jeannette',
    'hoho', 'nonni',
    'james', 'mandi',
    'charles', 'julie',
    'tori'
]

export type Character = HasId & HasHitBox & HasRenderer & CanBeControlled & CanCarry & CanChop & CanBread & {
    characterId: CharacterId
}

export const createCharacter = (characterId: CharacterId, controllerId: ControllerId, x?: Dimension, z?: Dimension): Character => ({
    characterId,
    ...createWithId(),
    ...createWithHitbox('2studs', '2studs', x, z),
    ...createCanBeControlled(controllerId),
    ...createCanCarry(),
    ...createCanChop(),
    ...createCanBread(),
    render: create3DRenderer({ sprite: getSprite, padding: '2studs' })
});

function getSprite(character: Character){
    let animationState;

    if(character.breading){
        animationState = 'standing.carrying'
    } else if(character.chopping){
        animationState = 'chopping'
    } else if (character.moving) {
        animationState = character.carrying ? 'walking.carrying' : 'walking'
    } else {
        animationState = character.carrying ? 'standing.carrying' : 'standing'
    }

    return `characters/${character.characterId}/${animationState}/${character.facing}`
}

let numberOfLevels = 6

const characterLocks: Record<CharacterId, number> = {
    'truett': 0,
    'jeannette': 0,
    'hoho': 0,
    'nonni': 0,
    'james': 1 / numberOfLevels,
    'mandi': 1 / numberOfLevels,
    'josh': 3 / numberOfLevels,
    'tori': 3 / numberOfLevels,
    'charles': 6 / numberOfLevels,
    'julie': 6 / numberOfLevels,
    'joy': 9 / numberOfLevels,
    'miles': 9 / numberOfLevels,
    'isaac': 9 / numberOfLevels,
    'lydia': 9 / numberOfLevels,
}

export function isCharacterUnlocked(state: GameState, characterId: CharacterId){
    return state.devMode || getAverageStars(state) >= characterLocks[characterId]
}

function getAverageStars(state: GameState){
    let scores = Object.values(state.highScores);
    if(scores.length === 0) return 0;
    return scores.reduce( (total: number, score) => total + score, 0) / scores.length
}