export type Tile = GameObject & HasHitBox | (() => GameObject & HasHitBox) | null;

type C = Character | null

export function tileLevel(matrix: (c1: C, c2: C, c3: C, c4: C) => Tile[][], state: GameState): GameObject[]{
    let objects: GameObject[] = [];

    let z = coalesceZ(`${GAME_HEIGHT_STUDS - 2}studs`) as number;

    let c1 = state.selectedCharacters[1] === null ? null : createCharacter(state.selectedCharacters[1], 1)
    let c2 = state.selectedCharacters[2] === null ? null : createCharacter(state.selectedCharacters[2], 2)
    let c3 = state.selectedCharacters[3] === null ? null : createCharacter(state.selectedCharacters[3], 3)
    let c4 = state.selectedCharacters[4] === null ? null : createCharacter(state.selectedCharacters[4], 4)


    for(let row of matrix(c1, c2, c3, c4)){
        let x = 0;
        for(let column of row){
            if(column !== null){
                let object = typeof column === 'function' ? column() : column;
                if(!objects.includes(object)){
                    if(isCuttingBoard(object) || canProvide(object)){
                        let wall = createWall(x, z);
                        objects.push(wall, object)
                        objects = wall.handleDropped(object, objects, 1);
                    } else {
                        Object.assign(object.hitbox, {
                            x, z
                        })
                        objects.push(object)
                    }
                }
            }

            x = x + coalesceX('2studs')
        }
        z = z - coalesceZ('2studs')
    }

    return objects;
}