import { coalesceX, coalesceY, coalesceZ, Dimension, Pixels, Vector } from "Dimensions";

export type Origin = {
    x: Pixels;
    y: Pixels;
}

export type HasOrigin = {
    origin: Origin
}

export const hasOrigin = (x: any): x is HasOrigin => typeof x.origin !== 'undefined'


export function createWithOrigin( x: Dimension = 0,  y: Dimension = 0): HasOrigin {
    return {
        origin: {
            x: coalesceX(x),
            y: coalesceY(y)
        }
    }
}
