
const loadedImages: Record<string, HTMLImageElement> = {};

const loadedAudioTracks: Record<string, HTMLAudioElement> = {};

const aliasedAudioTracks: Record<string, HTMLAudioElement> = {};

export const AssetManager = {

    getImage(path: string){
        if(!(path in loadedImages)){
            loadedImages[path] = new Image();
            loadedImages[path].src = `assets/${path}`;
        }
        return loadedImages[path];
    },

    loadImage(path: string){
        return new Promise( resolve => {
            const image = AssetManager.getImage(path);
            image.addEventListener('load', () => {
                resolve(image)
            })
        })
    },

    getAudio(path: string){
        if(!(path in loadedAudioTracks)){
            loadedAudioTracks[path] = new Audio(`assets/${path}`);
        }
        return loadedAudioTracks[path];
    },

    loadAudio(path: string){
        return new Promise( resolve => {
            const audio = AssetManager.getAudio(path);
            audio.addEventListener('canplay', () => {
                resolve(audio)
            })
        })
    },

    loadAliasedAudio(alias: string, path: string){
        if(!(alias in aliasedAudioTracks)){
            aliasedAudioTracks[alias] = new Audio(`assets/${path}`);
            aliasedAudioTracks[alias].loop = true;
        }
        return {
            play: (volume: number = 1) => {
                this.currentlyPlaying.push(alias)
                aliasedAudioTracks[alias].volume = volume;
                aliasedAudioTracks[alias].play();
            }
        }
    },

    currentlyPlaying: [] as string[],
    clearAudio(){
        for(let alias in aliasedAudioTracks){
            if(!this.currentlyPlaying.includes(alias)){
                aliasedAudioTracks[alias].pause();
                aliasedAudioTracks[alias].currentTime = 0
            }
        }
        this.currentlyPlaying = [];
    }
}