import { Dimension } from "Dimensions";
import { combineRenderers, create3DRenderer, createContentRenderer } from "Renderer";
import { CanBlockMovement, createCanBlockMovement, createWithCounterSpace, createWithHitbox, createWithId, HasRenderer, HasCounterSpace, HasHitBox, HasId, CanHandleDrop, hasHitbox, CanProvide, createCanProvide, canBlockMovement } from "Traits";
import { createFoodItem, FoodItemId } from "./FoodItem";

export type Bucket = HasId & HasHitBox & CanProvide & HasRenderer;

export const createBucket = (content: FoodItemId, x?: Dimension, z?: Dimension): Bucket => ({
    ...createWithId(),
    ...createWithHitbox('2studs', '2studs', x, z, '1studs'),
    ...createCanProvide(() => createFoodItem(content)),
    render: combineRenderers(
        create3DRenderer('bucket'),
        createContentRenderer({ sprite: 'bucket', content  })
    ),
})
