export const WIDTH = window.innerWidth
export const HEIGHT = window.innerHeight

export const GAME_WIDTH_STUDS = 40;
export const GAME_HEIGHT_STUDS = 14;

export const font = {
    small: WIDTH*0.015,
    medium: WIDTH*0.03,
    large: 60
}

export type Dimension = string | number;

export type Pixels = number & {
    __pixels__: true;
}

// @ts-ignore
window.toStuds = function(a: number){
    return a / (WIDTH / GAME_WIDTH_STUDS)
}

export function coalesceX(a: Dimension): Pixels {
    if (typeof a === 'number') return a as Pixels;
    if(a.endsWith('%')) return parsePercent(a, WIDTH);
    if (a.endsWith('studs')) return parseFloat(a) * (WIDTH / GAME_WIDTH_STUDS) as Pixels;
    throw Error(`Cannot Coalesce "${a}"`)
}

export function coalesceZ(a: Dimension): Pixels {
    if (typeof a === 'number') return a as Pixels;
    if(a.endsWith('%')) throw Error('Does a % measurement in the z axis make sense?')
    if (a.endsWith('studs')) return parseFloat(a) * (WIDTH / GAME_WIDTH_STUDS) as Pixels;
    throw Error(`Cannot Coalesce "${a}"`)
}

export function coalesceY(a: Dimension): Pixels {
    if (typeof a === 'number') return a as Pixels;
    if(a.endsWith('%')) return parsePercent(a, HEIGHT);
    if (a.endsWith('studs')) return parseFloat(a) * (WIDTH / GAME_WIDTH_STUDS * 1.4) as Pixels;
    throw Error(`Cannot Coalesce "${a}"`)
}

function parsePercent(a: string, containerSize: number): Pixels {
    return parseFloat(a) / 100 * containerSize as Pixels
}

export type Direction = 'north' | 'south' | 'east' | 'west';

export type Vector = {
    x: number;
    z: number;
}

export const createVectors = (speed: Dimension): Record<Direction, Vector> => {
    return {
        north: {
            x: 0,
            z: +coalesceZ(speed)
        },
        south: {
            x: 0,
            z: -coalesceZ(speed)
        },
        east: {
            x: +coalesceX(speed),
            z: 0
        },
        west: {
            x: -coalesceX(speed),
            z: 0
        }
    }
}

export const createVector = (speed: Dimension, direction: Direction) => createVectors(speed)[direction]