import { coalesceX, coalesceY, Dimension } from "Dimensions";
import {  create2DRenderer } from "Renderer";
import { GameObject, GameState } from "State";
import {  createWithBoundingbox, createWithId, HasBoundingbox, HasId, HasOverlay, HasRenderer, Renderer } from "Traits";

type Overlay = HasId & HasBoundingbox & HasOverlay & HasRenderer & {
    color: string
};

export const createOverlay = (color: string = 'rgba(0, 0, 0, 0.75)'): Overlay => ({
    ...createWithId(),
    ...createWithBoundingbox('100%', '100%', 0, 0),
    hasOverlay: true,
    render,
    color
})

const render = function(_1: GameState, { boundingbox, color  }: Overlay, ctx: CanvasRenderingContext2D){
    const { x, y, width, height } = boundingbox
    ctx.fillStyle = color;
    ctx.fillRect(x, y, width, height)
} as Renderer;