export const loadLevelMenu = (state: GameState) => ([
    createGameText({
        text: 'Select a Level',
        baseline: 'top',
        align: 'left'
    }, '5%', '5%'),
    ...Levels.map((level, index) => (
        createButton({text: `${index+1}. ${isLevelUnlocked(state, level.id) ? level.name : 'Locked'}`, align: 'right'}, index === 0, '50%', 12 * (index + 2) + '%', (state): GameState => (
            isLevelUnlocked(state, level.id)
                ? {
                    ...state,
                    selectedLevel: level,
                    selectedCharacters: { 1: null, 2: null, 3: null, 4: null },
                    objects: loadCharacterMenu()
                }
                : state
        ))
    )),
    ...Levels.map((level, index) => (
        createStarDisplay(state.highScores[level.id] || 0, '10%', '57%',  12 * (index + 2) - 0.2 + '%')
    ))
])