import { GameObject } from "State";
import { CanBeCarried } from "./CanBeCarried";
import { HasHitBox } from "./HasHitbox";

type Provide = () => GameObject & HasHitBox & CanBeCarried

export type CanProvide = {
    canProvide: true;
    provide: Provide
}

export const canProvide = (x: any): x is CanProvide => Boolean(x?.canProvide)

export const createCanProvide = (provide: Provide): CanProvide => ({
    canProvide: true,
    provide
})
