import { coalesceX, Dimension, HEIGHT } from "Dimensions";
import { FoodItemId, Tray } from "Objects";
import { Animations, getSpriteCoords } from "Renderer";
import { AssetManager } from "Renderer";
import { GameObject, GameState } from "State";
import { HasHitBox, Renderer } from "Traits";
import { CanBeSpicy } from "Traits/CanBeSpicy";
import { renderFoodItemsInArea } from "./renderFoodItemsInArea";


export const createSpicyIconRenderer = <T extends GameObject & HasHitBox & CanBeSpicy>(): Renderer => {
    const renderer = (state: GameState, object: T, ctx: CanvasRenderingContext2D) => {
        const animation = Animations['spicy-icon'];

        const scale = object.hitbox.dx / 3 / animation.size.width;

        const width = object.hitbox.dx / 3
        const height = animation.size.height * scale;
        const x = object.hitbox.x + width
        const y = HEIGHT - (object.hitbox.z + object.hitbox.y + height + 15);



        if(object.isSpicy){
            ctx.drawImage(
                AssetManager.getImage(`spicy-icon.png`),
                x, y, width, height
            )
        }
    }

    return renderer as Renderer;
}
