import { Controls } from "Controls";
import { coalesceX, coalesceY, Dimension } from "Dimensions";
import { createButtonRenderer, CreateButtonRendererOptions } from "Renderer";
import { GameObject, GameState, updateGameObjectById, updateGameObjectsWhere } from "State";
import { createWithBoundingbox, createWithId, createWithOrigin, filter, HasBoundingbox, HasId, HasOrigin, HasOverlay, HasRenderer } from "Traits";

let lastSelectionChange: null | number = null

export type Button = HasId & HasOrigin & HasRenderer & HasOverlay & {
    isButton: true;
    isSelected: boolean;
    createdAt: number;
    callback: (state: GameState) => GameState;
};

export const isButton = (x: any): x is Button => x && x.isButton;

export const createButton = (options: CreateButtonRendererOptions<Button>, isSelected: boolean, x: Dimension = 0, y: Dimension = 0, callback: Button['callback']): Button => ({
    ...createWithId(),
    ...createWithOrigin(x, y),
    render: createButtonRenderer(options),
    isSelected,
    isButton: true,
    hasOverlay: true,
    createdAt: Date.now(),
    callback
})

export function buttonReducer(object: GameObject, state: GameState) {
    if (!isButton(object)) return state;

    let button = object as Button;
    let objects = state.objects;


    for (let controller of Controls) {
        if (controller.justPressed('primaryAction') && button.isSelected && button.createdAt < Date.now() - 500) {
            return button.callback(state);
        }

        const justChangedSelection = lastSelectionChange !== null && lastSelectionChange > Date.now() - 250;
        if ((controller.isPressed('down') || controller.isPressed('up')) && button.isSelected && !justChangedSelection) {
            let direction = controller.isPressed('down') ? 1 : -1;

            lastSelectionChange = Date.now();

            button = {
                ...button,
                isSelected: false
            }

            objects = updateGameObjectById(objects, button)

            let buttons = filter(objects, isButton)
            for (let i = 0; i < buttons.length; i++) {
                let currentButton = buttons[i];
                let nextIndex = i + direction;
                if (nextIndex >= buttons.length) nextIndex = 0;
                if (nextIndex < 0) nextIndex = buttons.length - 1;
                let nextButton = buttons[nextIndex];
                if (currentButton.id === button.id) {
                    nextButton = {
                        ...nextButton,
                        isSelected: true
                    }
                    objects = updateGameObjectById(objects, nextButton)
                }
            }
        }

        if (controller.justReleased('up') || controller.justReleased('down')) {
            lastSelectionChange = null;
        }
    }

    return {
        ...state,
        objects
    };
}