

import { coalesceX, font, HEIGHT, WIDTH } from "Dimensions";
import { loadFinishedLevelMenu } from "Menus";
import { AssetManager } from "Renderer";
import { MS_PER_FRAME } from "startGame";
import { GameObject, GameState } from "State";
import { createHasOverlay, createWithId, filter, HasId, HasOverlay, HasRenderer, Renderer } from "Traits";

export type Timer = HasId & HasRenderer & HasOverlay & {
    isTimer: true;
    startedAt: number;
    duration: number;
}

export const isTimer = (x: any): x is Timer => x && x.isTimer

export const createTimer = (startedAt: number, duration: number): Timer => ({
    ...createWithId(),
    ...createHasOverlay(),
    startedAt,
    duration,
    isTimer: true,
    render
})

const sideLength = coalesceX('3.5studs')

const render = (({ frameNumber, objects }: GameState, timer: Timer, ctx: CanvasRenderingContext2D) => {
    ctx.beginPath();
    ctx.fillStyle = 'rgba(0, 0, 0, 0.5)';
    ctx.drawImage(
        AssetManager.getImage(`clock/1.png`),
        0, HEIGHT - sideLength - 10, sideLength, sideLength
    )
    ctx.fill();

    const elapsed = frameNumber - timer.startedAt

    const remaining = timer.duration - elapsed

    const totalSeconds = Math.round(remaining * MS_PER_FRAME / 1000)

    const minutes = Math.floor(totalSeconds / 60);

    const seconds = Math.round(totalSeconds % 60);

    const time = minutes.toString().padStart(2, '0') + ":" + seconds.toString().padStart(2, '0')

    ctx.beginPath();
    ctx.font = `italic ${font.medium}px apercu-medium`;
    ctx.textBaseline = 'top';
    ctx.strokeStyle = 'black';
    ctx.lineWidth = 8;
    ctx.fillStyle = 'rgba(255, 255, 255, 1)';
    ctx.strokeText(`${time}`, 15, HEIGHT - (sideLength*.8))
    ctx.fillText(`${time}`, 15, HEIGHT - (sideLength*.8))
}) as Renderer;

export function timerReducer(object: GameObject, state: GameState){
    if(!isTimer(object)) return state;

    const timer = object as Timer;

    const elapsed = state.frameNumber - timer.startedAt

    const remaining = timer.duration - elapsed

    if(remaining <= 0 && !state.isPaused){
        return {
            ...state,
            isPaused: true,
            canPause: false,
            objects: [
                ...state.objects,
                ...loadFinishedLevelMenu(state)
            ]
        }
    } else {
        return state
    }
}
