import { FoodItemId } from "Objects";
import type { GameObject, GameState } from "State";
import { HowWillWeTopThis } from "./HowWillWeTopThis";
import { LetsGetSaucy } from "./LetsGetSaucy";
import { TheOriginal } from "./TheOriginal";
import { TrulyAPleasure } from "./TrulyAPleasure";
import { TurnUpTheHeat } from "./TurnUpTheHeat";

export type Level = {
    id: number;
    name: string;
    targetScores: [ number, number, number ];
    getObjects(state: GameState): GameObject[];
    tutorialEntries: FoodItemId[]
}

export const Levels: Level[] = [
    TheOriginal,
    HowWillWeTopThis,
    LetsGetSaucy,
    TurnUpTheHeat,
    TrulyAPleasure
]

export function isLevelUnlocked(state: GameState, levelId: number){
    if(state.devMode) return true;
    let levelIndex = Levels.findIndex( level => level.id === levelId);
    let formerLevel = Levels[levelIndex-1];
    if(!formerLevel) return true;
    return Boolean(state.highScores[formerLevel.id]);
}
