import { coalesceX, coalesceY, coalesceZ, Dimension, Pixels } from "Dimensions";

export type Boundingbox = {
    x: Pixels;
    y: Pixels;
    width: Pixels;
    height: Pixels;
}

export type HasBoundingbox = {
    boundingbox: Boundingbox
}

export const HasBoundingbox = (x: any): x is HasBoundingbox => typeof x.boundingbox !== 'undefined'

export function createWithBoundingbox(width: Dimension, height: Dimension, x: Dimension = 0, y: Dimension = 0): HasBoundingbox {
    return {
        boundingbox: {
            width: coalesceX(width),
            height: coalesceY(height),
            x: coalesceX(x),
            y: coalesceY(y)
        }
    }
}