import { coalesceX, Dimension, font, HEIGHT, Pixels } from "Dimensions";
import { Button } from "Menus";
import { Animations, Animation } from "Renderer";
import { AssetManager } from "Renderer";
import { MS_PER_FRAME } from "startGame";
import { GameObject, GameState } from "State";
import { Renderer, HasHitBox, Hitbox, HasBoundingbox } from "Traits";

export const createButtonRenderer = (options: CreateButtonRendererOptions<Button>): Renderer => {
    const renderer = function(this: HasBoundingbox, state: GameState, button: Button, ctx: CanvasRenderingContext2D){
        const { text, fade, align, baseline, color } = processButtonRendererOptions(options, button, state);

        let { x, y } = button.origin;

        if(text === null) return

        if(fade){
            const { startAt, duration } = fade;
            const elapsed = state.frameNumber - startAt;
            const finalFrame = duration / MS_PER_FRAME ;
            ctx.globalAlpha = elapsed / finalFrame
        }

        ctx.fillStyle = color;
        ctx.strokeStyle = color;

        ctx.font = `${font.medium}px apercu-medium`
        ctx.textAlign = align;
        ctx.textBaseline = baseline;

        const { width, fontBoundingBoxDescent, actualBoundingBoxLeft } = ctx.measureText(text)

        ctx.fillText(
            text,
            x,
            y,
        );

        if(button.isSelected){
            ctx.beginPath();
            ctx.setLineDash([ 5, 5 ])
            ctx.moveTo(x - actualBoundingBoxLeft, y + fontBoundingBoxDescent);
            ctx.lineTo(x + width - actualBoundingBoxLeft, y + fontBoundingBoxDescent);
            ctx.stroke();
            ctx.setLineDash([])
        }

        ctx.globalAlpha = 1;
    }

    return renderer as Renderer;
}

type TextOption<T> = string | ((object: T, state: GameState) => string);

export type CreateButtonRendererOptions<T> = TextOption<T> | {
    text: TextOption<T>
    fade?: { startAt: number, duration: number }
    align?: CanvasTextAlign;
    baseline?: CanvasTextBaseline
    color?: string
};

export function processButtonRendererOptions<T>(options: CreateButtonRendererOptions<T>, object: T, state: GameState) {
    let text, fade;

    let align: CanvasTextAlign = 'center';
    let baseline: CanvasTextBaseline = 'middle'
    let color = 'black'

    if(options === null){
        text = null;
    } else if (typeof options === 'function') {
        text = options(object, state);
    } else if (typeof options !== 'object') {
        text = options
    } else {
        text = typeof options.text === 'function' ? options.text(object, state) : options.text
        fade = options.fade
        align = options.align || align
        baseline = options.baseline || baseline
        color = options.color || color
    }

    return { text, fade, align, baseline, color  }
}

