

import { coalesceX, Dimension, font, HEIGHT, WIDTH } from "Dimensions";
import { AssetManager } from "Renderer";
import { MS_PER_FRAME } from "startGame";
import { GameState } from "State";
import { createHasOverlay, createWithBoundingbox, createWithId, filter, HasBoundingbox, HasId, HasOverlay, HasRenderer, Renderer } from "Traits";
import { FoodItemId } from "./FoodItem";
import { isOrderBoard, Order } from "./OrderBoard";

export type ScoreCounter = HasId & HasRenderer & HasOverlay & HasBoundingbox & {
    isScoreCounter: true;
}

export const isScoreCounter = (x: any): x is ScoreCounter => x && x.isScoreCounter

const defaultWidth = coalesceX('3.5studs')

export const createScoreCounter = (sideLength: Dimension = defaultWidth, x: Dimension = WIDTH - (defaultWidth/2), y: Dimension = HEIGHT - (defaultWidth/2)): ScoreCounter => ({
    ...createWithId(),
    ...createHasOverlay(),
    ...createWithBoundingbox(sideLength, sideLength, x, y),
    isScoreCounter: true,
    render
})


const render = ((state: GameState, counter: ScoreCounter, ctx: CanvasRenderingContext2D) => {

    let { x, y, width: sideLength } = counter.boundingbox;

    ctx.beginPath();
    ctx.fillStyle = 'rgba(0, 0, 0, 0.5)';

    ctx.drawImage(
        AssetManager.getImage(`score-counter/1.png`),
        x - (sideLength/2), y - (sideLength/2), sideLength, sideLength
    )
    ctx.fill();

    const finalScore = calculateFinalScore(state)

    ctx.beginPath();
    ctx.font = `italic ${font.large}px apercu-medium`;
    ctx.textBaseline = 'middle';
    ctx.textAlign = 'center';
    ctx.fillStyle = 'rgba(255, 255, 255, 1)';
    ctx.strokeStyle = 'black';
    ctx.lineWidth = 8;
    ctx.strokeText(`${finalScore}`, x - (sideLength*.1), y - (sideLength*.1))
    ctx.fillText(`${finalScore}`, x - (sideLength*.1), y - (sideLength*.1))
}) as Renderer;

const timeToDeliveryGreaterThan = (deadline: number, frameNumber: number) => (order: Order) =>
    (order.fullfilledAt || frameNumber) - order.placedAt > deadline  * 1000 / MS_PER_FRAME

const timeToDeliveryLessThan = (deadline: number, frameNumber: number) => (order: Order) =>
    (order.fullfilledAt || frameNumber) - order.placedAt < deadline  * 1000 / MS_PER_FRAME

export function calculateFinalScore({ objects, frameNumber }: GameState){
    let [ orderBoard ] = filter(objects, isOrderBoard);

    if(!orderBoard) return 0;

    const { firstDeadline, secondDeadline } = orderBoard;

    let score = -
        (orderBoard.orders.filter(timeToDeliveryGreaterThan(secondDeadline, frameNumber)).length
        + orderBoard.fullfilled.filter(timeToDeliveryGreaterThan(secondDeadline, frameNumber)).length)

    score += orderBoard.fullfilled
        .filter(timeToDeliveryGreaterThan(firstDeadline, frameNumber))
        .filter(timeToDeliveryLessThan(secondDeadline, frameNumber))
        .reduce(ordersToScore, 0)

    score += orderBoard.fullfilled
        .filter(timeToDeliveryLessThan(firstDeadline, frameNumber))
        .reduce(ordersToScore, 0) * 2

    return score;
}

const doubleScoringItems: FoodItemId[] = [
    'deluxe-sandwich',
    'deluxe-sandwich-no-pickles',
    'spicy-deluxe-sandwich',
    'spicy-deluxe-sandwich-no-pickles',
    'fruit-cup'
]

const ordersToScore = (score: number, order: Order) => {
    if(doubleScoringItems.some( item => order.foodItems.includes(item))){
        return score + 2
    }
    return score + 1
}